import React from 'react'
import { useRecoilValue } from 'recoil'

import { BOX_STATE_ID, selectLatestSubmissionByBoxId } from '../../data/selectors'
import { PendingBoxReview as Component } from './PendingBoxReview'
import { Fallback } from './Fallback'
import { withSuspense } from '../withSuspense'

export interface PendingBoxReviewProps {
  boxId: string | number
}

export const PendingBoxReview = withSuspense(
  ({ boxId }: PendingBoxReviewProps): JSX.Element => {
    const submission = useRecoilValue(selectLatestSubmissionByBoxId(Number(boxId)))

    if (!submission || submission.boxStateId !== BOX_STATE_ID.pending)
      return <h2>Box {boxId} not found or it is not in Pending state.</h2>

    return <Component submission={submission} />
  },
  () => <Fallback />
)
