import React from 'react'
import { useRecoilValue } from 'recoil'

import { selectBoxById, selectBoxTaxaByBoxId } from '../../data/selectors'
import { withSuspense } from '../withSuspense'
import { BoxTaxonomy as Component } from './BoxTaxonomy'
import { Fallback } from './Fallback'

export interface BoxTaxonomyProps {
  boxId: string | number
}

export const BoxTaxonomy = withSuspense(({ boxId }: BoxTaxonomyProps): JSX.Element => {
  const box = useRecoilValue(selectBoxById(Number(boxId)))
  const boxTaxa = useRecoilValue(selectBoxTaxaByBoxId(Number(boxId)))

  if (!box) return <h2>Box {boxId} not found.</h2>

  return <Component box={box} boxTaxa={boxTaxa} />
}, Fallback)
