/** @jsxImportSource @emotion/react */
import React, { useEffect } from 'react'
import { useRecoilValue } from 'recoil'
import IconButton from '@mui/material/IconButton'
import MenuIcon from '@mui/icons-material/Menu'

import { QuickNavigateField } from '../QuickNavigateField'
import { isLocalhost, isStagingApi } from '../../lib/environment'
import { selectHasUser } from '../../data/selectors'

export interface AppHeaderProps {
  openNavigation: () => void
}

export const AppHeader = ({ openNavigation }: AppHeaderProps): JSX.Element => {
  const hasUser = useRecoilValue(selectHasUser)

  useEffect(() => {
    if (isLocalhost) {
      document.title = `${document.title} (local)`
    } else if (isStagingApi) {
      document.title = `${document.title} (staging)`
    }
  }, [])

  const conditionalVisibility = hasUser ? 'visible' : 'hidden'

  return (
    <header
      css={{
        height: '3em',
        background: '#064270',
        color: 'white',
        display: 'flex',
        alignItems: 'center',
        padding: '0 0.5em',
      }}
    >
      <IconButton onClick={openNavigation}>
        <MenuIcon sx={{ fill: 'white' }} />
      </IconButton>
      <h1 css={{ fontWeight: 500, letterSpacing: '0.05em', fontSize: '200%' }}>
        INSTAR
        {isStagingApi && <span css={{ color: 'yellow' }}> STAGING</span>}
      </h1>

      <div css={{ margin: '0 0.25em 0 auto', visibility: conditionalVisibility }}>
        <QuickNavigateField css={{ '.MuiInputBase-root': { background: 'white' } }} />
      </div>
    </header>
  )
}
