/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react'
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft'
import MuiIconButton from '@mui/material/IconButton'
import MuiButton from '@mui/material/Button'
import MuiSkeleton from '@mui/material/Skeleton'
import MuiCircularProgress from '@mui/material/CircularProgress'
import MenuIcon from '@mui/icons-material/Menu'
import dayjs from 'dayjs'
import produce from 'immer'
// import log from 'loglevel'

import { useSitesMapController } from '../SitesMap/useSitesMapController'
import { SitesMap } from '../SitesMap/SitesMap'
import { ReportDataRow, Site } from '../../types'
import { PublicMapSite } from './types'
import { Filter, FilterChips } from '../Filters'
import { intersection, range, uniq } from 'lodash'
import { useUpdatableState } from '../../lib/useUpdatableState'
import {
  MethodCriterionField,
  ProjectCriterionField,
  TaxonomyCriterionField,
  TypeCriterionField,
  UsStateCriterionField,
} from './CriterionFields'
import { formatNumber } from '../../lib/formats'

import { LinearProgress } from '../LinearProgress'
import { Title } from '../Title'
import { downloadWorkbookAsCsv } from '../../lib/excel/downloadWorkbook'
import { pluralize } from '../../lib/pluralize'
import { SelectField } from '../SelectField'
import { useRecoilCallback, useRecoilState } from 'recoil'
import { useNotifications } from '../AppNotifications'
import { usePublicApi } from './publicApi'
import { publicSitesAtom, selectPublicTaxaSiteIdsByTaxonId } from './selectors'
import { fetchPublicSites } from './calls/fetchPublicSites'

import { ReadOnlyField } from '../ReadOnlyField'
import { fetchPublicCsvDownload } from './calls/fetchPublicCsvDownload'
import { createSingleTableWorkbook } from '../../lib/excel/singleTableUtil'
import { CriterionOption } from '../CriterionAutocomplete'

interface CriteriaFields {
  taxon: CriterionOption<number> | null
  project: CriterionOption<number> | null
  usState: CriterionOption<number> | null
  site: CriterionOption<number> | null
  fromYear: number
  toYear: number
  type: CriterionOption<number> | null
  method: CriterionOption<number> | null
}

const getInitialCriteriaFields = (): CriteriaFields => ({
  taxon: null,
  project: null,
  usState: null,
  site: null,
  fromYear: 0,
  toYear: 0,
  type: null,
  method: null,
})

export const PublicMap = (): JSX.Element => {
  const publicApi = usePublicApi()

  const [displayFilters, setDisplayFilters] = useState<Filter<PublicMapSite>[]>([])
  const [filtered, setFiltered] = useState<PublicMapSite[]>([])
  const [isFetchingTaxonSites, setIsFetchingTaxonSites] = useState(false)
  const [cancelDownload, setCancelDownload] = useState<(() => void) | undefined>()

  const [isSidebarOpen, setIsSidebarOpen] = useState(true)
  const notifications = useNotifications()

  // SITES

  const [sites, setSites] = useRecoilState(publicSitesAtom)
  const [isFetchingSites, setIsFetchingSites] = useState(true)

  useEffect(() => {
    const accumulateSites = async (from: number, to: number) => {
      const fetched = await fetchPublicSites(publicApi)(from, to)
      setSites(
        produce((draft) => {
          draft.push(...fetched)
        })
      )
    }

    Promise.all([accumulateSites(0, 10000), accumulateSites(10000, 20000), accumulateSites(20000, Infinity)])
      .then(() => {
        setIsFetchingSites(false)
      })
      .catch((err) => {
        notifications.error(`Failed to fetch sites: ${err}`)
      })
  }, [])

  const getSiteIdsByTaxonId = useRecoilCallback<[number], Promise<number[]>>(
    ({ snapshot }) =>
      async (taxonId: number) =>
        snapshot.getPromise(selectPublicTaxaSiteIdsByTaxonId(taxonId)).catch((err) => {
          notifications.error(`Failed to retrieve taxon data: ${err}`)
          return []
        })
  )

  // CRITERIA STATE

  const [yearState, setYearState] = useState({
    minYear: 0,
    maxYear: 0,
    shortcutYears: [] as number[],
    yearOptions: [] as { value: number; label: string }[],
  })
  const { minYear, maxYear, shortcutYears, yearOptions } = yearState

  useEffect(() => {
    if (isFetchingSites) return

    const maxYear = dayjs().year()
    const minYear = sites.reduce((acc, site) => Math.min(acc, ...site.years), maxYear)
    const numberOfShortcuts = 7
    const shortcutYears = range(maxYear - numberOfShortcuts + 1, maxYear + 1)
    const yearOptions = range(maxYear, minYear - 1, -1).map((year) => ({
      value: year,
      label: String(year),
    }))

    setYearState({ minYear, maxYear, shortcutYears, yearOptions })
    updateCriteria((draft) => {
      draft.fromYear = minYear
      draft.toYear = maxYear
    })
  }, [isFetchingSites])

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [criteria, setCriteria, updateCriteria] = useUpdatableState<CriteriaFields>(getInitialCriteriaFields(), [])
  const makeSetFieldValue = (fieldName: keyof CriteriaFields) => (value: unknown) => {
    updateCriteria((draft) => {
      Object.assign(draft, { [fieldName]: value })
    })
  }
  const resetField = (fieldName: keyof CriteriaFields) => {
    updateCriteria((draft) => {
      Object.assign(draft, { [fieldName]: getInitialCriteriaFields()[fieldName] })
    })
  }

  // FILTERS

  useEffect(() => {
    const criteriaFilters: Filter<PublicMapSite>[] = []
    const fetchIdsPromises: Promise<number[]>[] = []

    if (criteria.project) {
      const { project } = criteria
      criteriaFilters.push({
        id: 'project',
        label: project.chipLabel,
        filterFn: ({ projectIds }) => projectIds.includes(project.id),
        onDelete: () => resetField('project'),
      })
    }

    if (criteria.usState) {
      const { usState } = criteria
      criteriaFilters.push({
        id: 'usState',
        label: usState.chipLabel,
        filterFn: ({ usStateId }) => usStateId === usState.id,
        onDelete: () => resetField('usState'),
      })
    }

    if (criteria.taxon) {
      const { taxon } = criteria
      fetchIdsPromises.push(getSiteIdsByTaxonId(taxon.id))
      criteriaFilters.push({
        id: 'taxon',
        label: taxon.chipLabel,
        filterFn: () => true, // pass all filter; filtering is handled below
        onDelete: () => resetField('taxon'),
      })
    }

    if (criteria.type) {
      const { type } = criteria
      criteriaFilters.push({
        id: 'type',
        label: type.chipLabel,
        filterFn: ({ typeIds }) => typeIds.includes(type.id),
        onDelete: () => resetField('type'),
      })
    }

    if (criteria.method) {
      const { method } = criteria
      criteriaFilters.push({
        id: 'method',
        label: method.chipLabel,
        filterFn: ({ methodIds }) => methodIds.includes(method.id),
        onDelete: () => resetField('method'),
      })
    }

    if (criteria.site) {
      const { site } = criteria
      criteriaFilters.push({
        id: 'site',
        label: site.chipLabel,
        filterFn: ({ id }) => id === site.id,
        onDelete: () => resetField('site'),
      })
    }

    if (criteria.fromYear !== minYear || criteria.toYear !== maxYear) {
      const { fromYear, toYear } = criteria
      const common = {
        id: 'dateCriteria',
        onDelete: () => {
          updateCriteria((draft) => {
            draft.fromYear = minYear
            draft.toYear = maxYear
          })
        },
      }

      if (fromYear === toYear) {
        criteriaFilters.push({
          ...common,
          label: `sampled in ${fromYear}`,
          filterFn: ({ years }) => years.includes(fromYear),
        })
      } else {
        const filterYears = range(fromYear, toYear + 1)
        criteriaFilters.push({
          ...common,
          label: `sampled between ${fromYear} and ${toYear}`,
          filterFn: ({ years }) => intersection(years, filterYears).length > 0,
        })
      }
    }

    setDisplayFilters(criteriaFilters)
    let cancel = false

    if (criteriaFilters.length === 0) {
      setIsFetchingTaxonSites(false)
      setFiltered(sites)
    } else if (fetchIdsPromises.length === 0) {
      setIsFetchingTaxonSites(false)
      if (isFetchingSites) return
      setFiltered(criteriaFilters.reduce<PublicMapSite[]>((acc, { filterFn }) => acc.filter(filterFn), sites))
    } else {
      setIsFetchingTaxonSites(true)
      Promise.all(fetchIdsPromises).then((fetchIdsResults) => {
        if (cancel || isFetchingSites) return
        const filterIds = intersection(...fetchIdsResults)
        const initialFiltered = criteriaFilters.reduce<PublicMapSite[]>(
          (acc, { filterFn }) => acc.filter(filterFn),
          sites
        )
        setIsFetchingTaxonSites(false)
        setFiltered(initialFiltered.filter(({ id }) => filterIds.includes(id)))
      })
    }

    return () => {
      cancel = true
    }
  }, [criteria, sites, isFetchingSites])

  // MAP

  const onSiteClick = (site: Site) => {
    const { id, name } = site
    updateCriteria((draft) => {
      draft.site = {
        id: id,
        optionLabel: `${name} [${id}]`,
        chipLabel: `from site ${name}`,
      }
    })
  }

  const controller = useSitesMapController({ sites: filtered, onSiteClick })

  useEffect(() => {
    controller.smartFitAll() // fit filtered sites; if no sites, smartFitAll does nothing
  }, [filtered])

  // DOWNLOADING

  const handleDownload = async () => {
    const includeYearCriteria = criteria.fromYear !== minYear || criteria.toYear !== maxYear || undefined
    const csvRows: ReportDataRow[] = []

    let cancel = false
    setCancelDownload(() => () => {
      // note extra function wrapper otherwise setter does not set to callback
      cancel = true
    })

    let currentOffset: number | null = 0

    while (currentOffset !== null && !cancel) {
      const { records, nextOffset } = await fetchPublicCsvDownload(publicApi)(
        {
          projectId: criteria.project?.id,
          usStateId: criteria.usState?.id,
          taxonomyId: criteria.taxon?.id,
          typeId: criteria.type?.id,
          methodId: criteria.method?.id,
          siteId: criteria.site?.id,
          fromYear: includeYearCriteria && criteria.fromYear,
          toYear: includeYearCriteria && criteria.toYear,
        },
        currentOffset
      )
      csvRows.push(...records)
      currentOffset = nextOffset
    }
    if (cancel) return

    if (csvRows.length === 0) {
      notifications.info('No data exists for the filtered samples.')
      setCancelDownload(undefined)
      return
    }

    const columnNames = csvRows.reduce<string[]>((acc, reportRow) => uniq([...acc, ...Object.keys(reportRow)]), [])
    const wb = createSingleTableWorkbook({
      wsName: 'data', // ignored in CSV
      columns: columnNames.map((columnName) => ({
        key: columnName,
        header: columnName,
      })),
      rows: csvRows,
    })
    if (!wb) return
    downloadWorkbookAsCsv(wb, `namc_data.csv`)
    setCancelDownload(undefined)
  }

  const handleCancelDownload = () => {
    if (!cancelDownload) return
    cancelDownload()
    setCancelDownload(undefined)
  }

  // RENDER

  const isFiltered = filtered.length !== sites.length
  const isFetching = isFetchingTaxonSites || isFetchingSites
  const isDownloadingCsv = !!cancelDownload

  const makeSetYear = (year: number) => () => {
    updateCriteria((draft) => {
      draft.fromYear = year
      draft.toYear = year
    })
  }

  return (
    <div css={{ height: '100%', display: 'flex' }}>
      <div
        css={{
          width: isSidebarOpen ? '400px' : 0,
          boxSizing: 'border-box',
          borderRight: '1px solid grey',
          overflow: 'hidden',
          transition: 'width 0.5s',
        }}
      >
        <div css={{ width: '395px' }}>
          <div css={{ display: 'flex', height: '40px', alignItems: 'center', position: 'relative' }}>
            <div css={{ fontWeight: 'bold', marginLeft: '0.7rem' }}>Sample Filters</div>
            <MuiIconButton css={{ position: 'absolute', right: 0 }} onClick={() => setIsSidebarOpen(false)}>
              <ChevronLeftIcon />
            </MuiIconButton>
          </div>
          <div css={{ padding: '0.5em' }}>
            <div
              css={{
                margin: '0.5em 0',
                display: 'grid',
                gap: '0.75em',
                gridTemplateColumns: '1fr 1fr',
                gridTemplateAreas: `
                  "project      project"
                  "usState      usState"
                  "taxon        taxon"
                  "type         type"
                  "method       method"
                  "fromYear     toYear"
                  "dates        dates"
            `,
              }}
            >
              <ProjectCriterionField
                gridArea="project"
                value={criteria.project}
                onChange={makeSetFieldValue('project')}
              />
              <TaxonomyCriterionField gridArea="taxon" value={criteria.taxon} onChange={makeSetFieldValue('taxon')} />
              <UsStateCriterionField
                gridArea="usState"
                value={criteria.usState}
                onChange={makeSetFieldValue('usState')}
              />
              <TypeCriterionField gridArea="type" value={criteria.type} onChange={makeSetFieldValue('type')} />
              <MethodCriterionField gridArea="method" value={criteria.method} onChange={makeSetFieldValue('method')} />
              {isFetchingSites ? (
                <ReadOnlyField gridArea="fromYear" label="From Year" value={<MuiSkeleton css={{ width: '8em' }} />} />
              ) : (
                <SelectField
                  gridArea="fromYear"
                  value={criteria.fromYear}
                  onChange={(newValue) => {
                    const newYear = Number(newValue)
                    updateCriteria((draft) => {
                      draft.fromYear = newYear
                      draft.toYear = Math.max(draft.toYear, newYear)
                    })
                  }}
                  label="From Year"
                  options={yearOptions}
                />
              )}
              {isFetchingSites ? (
                <ReadOnlyField gridArea="toYear" label="To Year" value={<MuiSkeleton css={{ width: '8em' }} />} />
              ) : (
                <SelectField
                  gridArea="toYear"
                  value={criteria.toYear}
                  onChange={(newValue) => {
                    const newYear = Number(newValue)
                    updateCriteria((draft) => {
                      draft.toYear = newYear
                      draft.fromYear = Math.min(draft.fromYear, newYear)
                    })
                  }}
                  label="To Year"
                  options={yearOptions}
                />
              )}
              <div css={{ gridArea: 'dates', display: 'flex' }}>
                {isFetchingSites ? (
                  <MuiSkeleton css={{ flex: 1 }} />
                ) : (
                  shortcutYears.map((year) => (
                    <a
                      key={year}
                      onClick={makeSetYear(year)}
                      css={{ display: 'inline-block', flex: 1, textAlign: 'center' }}
                    >
                      {year}
                    </a>
                  ))
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        css={{
          flex: 1,
          display: 'flex',
          flexDirection: 'column',
        }}
      >
        <div css={{ display: 'flex', height: '40px', padding: '0 0.5em' }}>
          {!isSidebarOpen && (
            <MuiIconButton
              onClick={() => {
                setIsSidebarOpen(true)
              }}
            >
              <MenuIcon />
            </MuiIconButton>
          )}
          <div css={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
            <Title>
              NAMC <Title.Subtitle>Aquatic Invertebrates Database</Title.Subtitle>
            </Title>
          </div>
        </div>
        <div css={{ padding: '0 0.5em', flex: 1, display: 'flex', flexDirection: 'column' }}>
          <div css={{ display: 'flex', alignItems: 'center', gap: '1em', padding: '0.5em 0' }}>
            <div css={{ flex: 1 }}>
              <FilterChips filters={displayFilters} joinLabel="and" />
            </div>
            <div>
              {(() => {
                if (isFetchingSites) return <span>Fetching sites...</span>
                if (!isFiltered) return <span>Showing all {sites.length} sites</span>
                if (filtered.length === 0) return <span>No samples found</span>
                return (
                  <span>
                    Found samples in <strong>{formatNumber(filtered.length)}</strong>{' '}
                    {pluralize(filtered.length, 'site')}
                  </span>
                )
              })()}
            </div>
            {isDownloadingCsv ? (
              <MuiButton onClick={handleCancelDownload} variant="outlined">
                Cancel Download <MuiCircularProgress size={20} sx={{ marginLeft: '1em' }} />
              </MuiButton>
            ) : (
              <MuiButton
                onClick={handleDownload}
                variant="outlined"
                disabled={isFetching || !isFiltered || filtered.length === 0}
              >
                Download CSV
              </MuiButton>
            )}
          </div>
          <div css={{ height: '4px' }}>{isFetching && <LinearProgress css={{ width: '100%' }} delayMs={200} />}</div>
          <div css={{ flex: 1, position: 'relative' }}>
            <SitesMap controller={controller} />
          </div>
        </div>
      </div>
    </div>
  )
}
