/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiAutocomplete from '@mui/material/Autocomplete'
import MuiTextField from '@mui/material/TextField'

export type CriterionOption<T> = {
  id: T
  optionLabel: string
  chipLabel: string
  disabled?: boolean
}

export interface CriterionFieldProps<T> {
  value: CriterionOption<T> | null
  onChange: (newValue: CriterionOption<T> | null) => void
  gridArea: string
  disabled?: boolean
}

export const CriterionAutocomplete = <T,>({
  value,
  gridArea,
  onChange,
  label,
  options,
  disabled,
}: CriterionFieldProps<T> & { label: string; options: CriterionOption<T>[] }): JSX.Element => (
  <MuiAutocomplete
    options={options}
    getOptionLabel={(option) => option.optionLabel}
    getOptionDisabled={(option) => !!option.disabled}
    renderInput={(params) => <MuiTextField {...params} label={label} />}
    onChange={(event, selected) => {
      onChange(selected)
    }}
    size="small"
    value={value && options.find((option) => option.id === value.id)}
    css={{ gridArea }}
    disabled={disabled}
  />
)
