/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiFormControl from '@mui/material/FormControl'
import MuiInputLabel from '@mui/material/InputLabel'

export interface ReadOnlyFieldProps {
  className?: string
  gridArea?: string
  label?: React.ReactNode
  minWidth?: string | number
  value: React.ReactNode
}

export const ReadOnlyField = ({
  className,
  gridArea,
  label,
  minWidth = 195, // match fields, prevent label collapse
  value,
}: ReadOnlyFieldProps): JSX.Element => {
  const displayValue = (() => {
    if (typeof value === 'boolean') return value ? 'Yes' : 'No'
    if (!value && value !== 0) return ''
    return value
  })()
  return (
    <MuiFormControl className={className} css={{ gridArea }}>
      <MuiInputLabel shrink>{label}</MuiInputLabel>
      <div
        css={{
          // below to match Material UI; there may be class/es which correctly apply the below with manual CSS
          minWidth,
          padding: '8.5px 14px',
          lineHeight: '1.4375em',
          letterSpacing: '0.00938em',
        }}
      >
        {displayValue}
      </div>
    </MuiFormControl>
  )
}
