import gql from 'graphql-tag'

import { Api } from '../../../data/api'

export const fetchPublicTaxaSiteIdsByTaxonIdQuery = gql`
  query ($taxonId: Int!) {
    taxaSites(taxonomyId: $taxonId)
  }
`

export const fetchPublicTaxaSiteIdsByTaxonId =
  (api: Api) =>
  async (taxonId: number): Promise<number[]> => {
    const data = await api.query<{
      taxaSites: number[]
    }>(fetchPublicTaxaSiteIdsByTaxonIdQuery, { taxonId })
    return data.taxaSites
  }
