import gql from 'graphql-tag'

import { ReportDataRow } from '../../../types'
import { Api } from '../../../data/api'

export interface PublicCsvDownloadInput {
  projectId?: number
  usStateId?: number
  typeId?: number
  methodId?: number
  taxonomyId?: number
  siteId?: number
  fromYear?: number
  toYear?: number
}

export const fetchPublicCsvDownloadQuery = gql`
  query ($offset: Int!, $filters: CSVFiltersInput!) {
    csvDownload(limit: 1000, offset: $offset, filters: $filters) {
      records
      nextOffset
    }
  }
`

export const fetchPublicCsvDownload =
  (api: Api) =>
  async (
    filters: PublicCsvDownloadInput,
    offset: number
  ): Promise<{ records: ReportDataRow[]; nextOffset: number | null }> => {
    const data = await api.query<{ csvDownload: { records: ReportDataRow[]; nextOffset: number | null } }>(
      fetchPublicCsvDownloadQuery,
      {
        filters,
        offset,
      }
    )

    return data.csvDownload
  }
