/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import { noop, times } from 'lodash'
import MuiDrawer from '@mui/material/Drawer'
import Skeleton from '@mui/material/Skeleton'
import { GrantAction, NAMCResource } from '@namcbugdb/common'

import { UserName } from './UserName'
import { useApi } from '../../data/api'
import { useNavigate } from '../AppRouter'
import { withSuspense } from '../withSuspense'
import { selectHasUser, selectMyUser } from '../../data/selectors'
import { appVersion } from '../../lib/environment'

export interface SideNavBarProps {
  className?: string
  isOpen: boolean
  onClose: () => void
}

const CenteredDiv = (props) => (
  <div css={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }} {...props} />
)

const Links = withSuspense<{ onClose: () => void }>(
  ({ onClose }) => {
    const navigate = useNavigate()
    const user = useRecoilValue(selectMyUser)

    const links = [
      {
        label: 'Boxes',
        onClick: () => navigate.boxes(),
        visible: user.hasPermission(NAMCResource.boxes, GrantAction.readOwn),
      },
      {
        label: 'Sites',
        onClick: () => navigate.sites(),
        visible: user.hasPermission(NAMCResource.sites, GrantAction.readAny),
      },
      {
        label: 'Customers',
        onClick: () => navigate.organizations(),
        visible: user.hasPermission(NAMCResource.organizations, GrantAction.readAny),
      },
      {
        label: 'Projects',
        onClick: () => navigate.projects(),
        visible: user.hasPermission(NAMCResource.projects, GrantAction.readAny),
      },
      {
        label: 'Taxonomy',
        onClick: () => navigate.taxa(),
        visible: user.hasPermission(NAMCResource.taxonomy, GrantAction.readAny),
      },
      {
        label: 'Models',
        onClick: () => navigate.models(),
        visible: user.hasPermission(NAMCResource.models, GrantAction.readAny),
      },
      {
        label: 'Predictors',
        onClick: () => navigate.predictors(),
        visible: user.hasPermission(NAMCResource.models, GrantAction.readAny),
      },
      {
        label: 'Translations',
        onClick: () => navigate.translations(),
        visible: user.hasPermission(NAMCResource.translations, GrantAction.readAny),
      },
      {
        label: 'Metrics',
        onClick: () => navigate.metrics(),
        visible: user.hasPermission(NAMCResource.metrics, GrantAction.readAny),
      },
      {
        label: 'Billing',
        onClick: noop,
        disabled: true,
        visible: user.hasPermission(NAMCResource.billing, GrantAction.readAny),
      },
      {
        label: 'Users',
        onClick: () => navigate.users(),
        visible: user.hasPermission(NAMCResource.individuals, GrantAction.readAny),
      },
      {
        label: 'Reports',
        onClick: () => navigate.reports(),
        visible: user.hasPermission(NAMCResource.sqlReports, GrantAction.readAny),
      },
    ]

    return (
      <>
        {links
          .filter(({ visible }) => visible)
          .map(({ label, onClick, disabled }) =>
            disabled ? (
              <span key={label} css={{ opacity: 0.5 }}>
                {label}
              </span>
            ) : (
              <a
                key={label}
                onClick={() => {
                  onClick()
                  onClose()
                }}
              >
                {label}
              </a>
            )
          )}
      </>
    )
  },
  () => (
    <>
      {times(10).map((i) => (
        <Skeleton key={i} width="8em" />
      ))}
    </>
  )
)

export const SideNavBar = ({ className, isOpen, onClose }: SideNavBarProps): JSX.Element => {
  const api = useApi()
  const navigate = useNavigate()
  const hasUser = useRecoilValue(selectHasUser)

  return (
    <MuiDrawer anchor="left" open={isOpen} onClose={() => onClose()} className={className}>
      <CenteredDiv css={{ minWidth: '20em', height: '100%' }}>
        <UserName />
        <div css={{ display: 'flex', gap: '1em' }}>
          {hasUser && (
            <a
              onClick={() => {
                navigate.profile()
                onClose()
              }}
            >
              Profile
            </a>
          )}
          <a
            onClick={() => {
              api.signOut()
            }}
          >
            Sign Out
          </a>
        </div>
        <div css={{ marginTop: '2em', fontSize: '125%', display: 'flex', flexDirection: 'column', gap: '1em' }}>
          <Links onClose={onClose} />
        </div>
        <CenteredDiv
          css={{
            marginTop: 'auto',
            padding: '1em 0',
            gap: '0.25em',
          }}
        >
          <span>v{appVersion}</span>
          <a href="https://github.com/namc-utah/namc-bug-db/issues/new" target="_blank" rel="noreferrer">
            Report Issue
          </a>
        </CenteredDiv>
      </CenteredDiv>
    </MuiDrawer>
  )
}
