import React from 'react'
import { useRecoilValue } from 'recoil'

import { selectSampleById, selectSorterUsers, selectMyUser } from '../../data/selectors'
import { SampleSorting as Component } from './SampleSorting'
import { Fallback } from './Fallback'
import { withSuspense } from '../withSuspense'

export interface SampleSortingProps {
  sampleId: string | number
}

export const SampleSorting = withSuspense(({ sampleId }: SampleSortingProps): JSX.Element => {
  const sorterUsers = useRecoilValue(selectSorterUsers)
  const user = useRecoilValue(selectMyUser)
  const sample = useRecoilValue(selectSampleById(Number(sampleId)))

  if (sample === null) return <h2>Sample {sampleId} not found.</h2>

  return <Component sorterUsers={sorterUsers} sample={sample} user={user} />
}, Fallback)
