import React from 'react'
import { useRecoilValue } from 'recoil'

import { selectTranslationById, selectNull } from '../../data/selectors'
import { withSuspense } from '../withSuspense'
import { TranslationDetail as Component } from './TranslationDetail'
import { Fallback } from './Fallback'

export type TranslationDetailProps =
  | {
      translationId: string | number
      isNew?: never
    }
  | {
      translationId?: never
      isNew: true // unconsumed; used to positively differentiate modes
    }

export const TranslationDetail = withSuspense(({ translationId }: TranslationDetailProps): JSX.Element => {
  const translation = useRecoilValue(translationId ? selectTranslationById(Number(translationId)) : selectNull)

  if (translationId && !translation) return <h2>Translation {translationId} not found.</h2>

  return <Component translation={translation} />
}, Fallback)
