/** @jsxImportSource @emotion/react */
import React from 'react'
import TextField, { StandardTextFieldProps } from '@mui/material/TextField'

import { ReadOnlyField } from '../ReadOnlyField'
import { CommonFieldProps } from '../../types'

interface DateFieldProps extends CommonFieldProps<string | null>, Omit<StandardTextFieldProps, 'value' | 'onChange'> {
  inputRef?: React.Ref<HTMLInputElement>
  max?: string
  min?: string
}

export const DateField = ({
  // CommonFieldProps
  className,
  disabled,
  error,
  gridArea,
  label,
  onChange,
  readOnly,
  value,

  // DateField
  inputRef,
  max,
  min,

  // StandardTextFieldProps
  ...restProps
}: DateFieldProps): JSX.Element => {
  if (readOnly) return <ReadOnlyField className={className} gridArea={gridArea} label={label} value={value} />

  const handleChange = ({ target: { value: newValue } }) => {
    if (min !== undefined && newValue < min) return
    if (max !== undefined && newValue > max) return
    onChange(newValue || null)
  }

  return (
    <TextField
      size="small"
      className={className}
      disabled={disabled}
      error={error}
      inputRef={inputRef}
      label={label}
      onChange={handleChange}
      value={value ?? ''}
      type="date"
      InputLabelProps={{ shrink: true }}
      InputProps={{ sx: { color: value ? undefined : '#868686' } }}
      css={{ gridArea }}
      {...restProps}
    />
  )
}
