/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiAccordion from '@mui/material/Accordion'
import MuiAccordionSummary from '@mui/material/AccordionSummary'
import MuiAccordionDetails from '@mui/material/AccordionDetails'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'

export interface AccordionPanelProps {
  value: boolean
  onChange: (newValue: boolean) => void
  locked?: boolean
  summary: React.ReactNode
  actions?: React.ReactNode
  children: React.ReactNode
}

export const AccordionPanel = ({
  value,
  onChange,
  locked,
  summary,
  actions,
  children,
}: AccordionPanelProps): JSX.Element => (
  <MuiAccordion
    expanded={value}
    onChange={(e, newValue) => {
      if (locked) return
      onChange(newValue)
    }}
    css={{ marginBottom: '1em' }}
  >
    <MuiAccordionSummary
      expandIcon={<ExpandMoreIcon color={locked ? 'disabled' : undefined} />}
      style={{ cursor: locked ? 'default' : undefined }} // deliberately using style here as override
    >
      <div css={{ display: 'flex', alignItems: 'center', width: '100%' }}>
        <div>{summary}</div>
        {actions && (
          <div
            css={{ margin: '0 0.5em 0 auto' }}
            onClick={(e) => {
              e.stopPropagation()
            }}
          >
            {actions}
          </div>
        )}
      </div>
    </MuiAccordionSummary>
    <MuiAccordionDetails>{children}</MuiAccordionDetails>
  </MuiAccordion>
)
