/** @jsxImportSource @emotion/react */
import React from 'react'

import { selectSitesByProjectId } from '../../data/selectors'
import { SitesMapPreview } from '../SitesMap'

export interface ProjectSitesMapPreviewProps {
  projectId: number
  projectName: string
}

export const ProjectSitesMapPreview = ({ projectId, projectName }: ProjectSitesMapPreviewProps): JSX.Element => {
  const sitesSelector = selectSitesByProjectId(projectId)
  return (
    <SitesMapPreview
      sitesSelector={sitesSelector}
      sitesListingSource={{ type: 'project', id: projectId, name: projectName }}
    />
  )
}
