import { api } from '@namcbugdb/common'
import { ASTNode } from 'graphql'
import { UseHostedReturn } from '@namcbugdb/react-common'
import log from 'loglevel'

import { Api, ApiError, LogLevel } from './Api'

const TIMEOUT_SECONDS = 30

const nextRequestNumber = (() => {
  let requestNumber = 1
  return () => String(requestNumber++)
})()

export class GraphApi implements Api {
  url: string
  auth: UseHostedReturn | null
  signOut: () => void

  constructor(url: string, auth: UseHostedReturn | null, signOut: () => void) {
    this.url = url
    this.auth = auth
    this.signOut = signOut
  }

  send = async <T>(query: ASTNode, variables?: { [key: string]: unknown }): Promise<T> => {
    const jwt = this.auth ? await this.auth?.getCurrentToken() : null
    const response = await api.apiCall<T>(this.url, jwt, query, variables, TIMEOUT_SECONDS * 1000)
    const { data, errors } = response
    if (errors || !data) {
      const errorMessagesString = errors?.map((error) => error.message).join(';') ?? 'unknown error or missing data'
      log.error(errorMessagesString, errors)
      // this.log('ERROR', `api`, {}) // TODO: enable with data once logging service implemented
      if (errors && errors[0]?.message === 'Authentication Error') this.signOut()
      throw new ApiError(errorMessagesString)
    }
    return data
  }

  queryAllPages = async <QueryT>(
    query: ASTNode,
    variables: { [key: string]: unknown },
    {
      dataProperty,
      onRecords,
      firstOffset = 0,
      lastOffset = Infinity,
    }: { dataProperty: string; onRecords?: (qty: number) => void; firstOffset?: number; lastOffset?: number }
  ): Promise<QueryT> => {
    let offset: number | null | undefined = firstOffset
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const allRecords: any = [] // type is the type of record

    while (offset !== null) {
      const lastData = await this.query<QueryT>(query, { ...variables, offset })
      const { records, nextOffset } = lastData[dataProperty]
      if (nextOffset === undefined)
        throw new ApiError('queryAllPages was called without the query returning nextOffset.')
      allRecords.push(...records)
      offset = nextOffset
      if (onRecords) onRecords(records.length)
      if (!offset || offset >= lastOffset) return { ...lastData, [dataProperty]: { records: allRecords } }
    }

    throw new Error('queryAllPages finished without returning data') // unreachable code; this is just to satisfy TS
  }

  query = async <T>(query: ASTNode, variables?: { [key: string]: unknown }): Promise<T> => {
    const requestNumber = nextRequestNumber()
    log.debug(
      `%cGraphApi query #${requestNumber}`,
      'color: black; background: lightgreen',
      query.loc?.source.body,
      variables
    )
    try {
      const response = await this.send<T>(query, variables)
      log.debug(`%cGraphApi response #${requestNumber}`, 'color: black; background: #0cb700', response)
      return response
    } catch (err) {
      log.debug(
        `%cGraphApi response #${requestNumber}%c %cERROR`,
        'color: black; background: lightgreen',
        'background: transparent',
        'color: white; background: red'
      )
      throw err
    }
  }

  mutation = async <T>(query: ASTNode, variables?: { [key: string]: unknown }): Promise<T> => {
    const requestNumber = nextRequestNumber()
    log.debug(
      `%cGraphApi mutation #${requestNumber}`,
      'color: black; background: #ffd17a',
      query.loc?.source.body,
      variables
    )

    try {
      const response = await this.send<T>(query, variables)
      log.debug(`%cGraphApi response #${requestNumber}`, 'color: black; background: orange', response)
      return response
    } catch (err) {
      log.debug(
        `%cGraphApi response #${requestNumber}%c %cERROR`,
        'color: black; background: orange',
        'background: transparent',
        'color: white; background: red'
      )
      throw err
    }
  }

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  log = async (level: LogLevel, message: string, context: Record<string, unknown>): Promise<void> => {
    // not implemented (logging service TBD)
  }
}
