import React from 'react'
import { useRecoilValue } from 'recoil'
import Skeleton from '@mui/material/Skeleton'

import { withSuspense } from '../withSuspense'
import { selectBoxSamplesByBoxId } from '../../data/selectors'
import { SamplesDataGrid } from '../SamplesDataGrid'

interface BoxDetailProps {
  boxId: number
}

export const BoxSamples = withSuspense(
  ({ boxId }: BoxDetailProps): JSX.Element => {
    const boxSamples = useRecoilValue(selectBoxSamplesByBoxId(boxId))
    return <SamplesDataGrid samples={boxSamples} sitesListingSource={{ type: 'box', id: boxId }} />
  },
  () => <Skeleton height="3em" variant="rectangular" />
)
