/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import Skeleton from '@mui/material/Skeleton'

import { selectModels } from '../../data/selectors'
import { ReadOnlyField } from '../ReadOnlyField'
import { withSuspense } from '../withSuspense'
import { AutocompleteArrayField, AutocompleteArrayFieldProps } from '../AutocompleteArrayField'

export type ModelsFieldProps = Omit<AutocompleteArrayFieldProps, 'options'>

export const ModelsField = withSuspense<ModelsFieldProps>(
  (props): JSX.Element => {
    const models = useRecoilValue(selectModels)
    const options = models.map(({ id: value, abbreviation: label }) => ({ value, label }))

    return <AutocompleteArrayField {...props} options={options} />
  },
  (props) => <ReadOnlyField {...props} value={<Skeleton />} />
)
