/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiChip from '@mui/material/Chip'

interface ChipProps {
  // variant?: 'standard' | 'reference'
  className?: string
  label: string
  textColor?: string
  chipColor?: string
  onDelete?: () => void
}

export const Chip = ({ className, label, chipColor, textColor, onDelete }: ChipProps): JSX.Element => (
  <MuiChip
    className={className}
    sx={{ backgroundColor: chipColor, color: textColor }}
    label={label}
    variant={chipColor ? 'filled' : 'outlined'}
    onDelete={onDelete}
  />
)
