import { atom, useRecoilValue } from 'recoil'
import { endpoints } from '../../config'
import { Api, GraphApi } from '../../data/api'

export const publicApiAtom = atom({
  key: 'publicApiAtom',
  default: new GraphApi(endpoints.public_graphql, null, () => undefined),
  dangerouslyAllowMutability: true,
})

export const usePublicApi = (): Api => useRecoilValue(publicApiAtom)
