/** @jsxImportSource @emotion/react */
import React from 'react'

import { withTooltip } from '../withTooltip'

interface FlagBadgeProps {
  label: string
  badgeColor: string
  className?: string
  tooltip?: string
  textColor?: string
  disabled?: boolean
}

export const Badge = ({ className, label, tooltip, badgeColor, textColor }: FlagBadgeProps): JSX.Element =>
  withTooltip(
    <span
      className={className}
      css={{
        background: badgeColor,
        color: textColor,
        height: '1.2em',
        padding: '0.1em 0.2em',
      }}
    >
      <span
        css={{
          width: '1em',
          display: 'inline-block',
          textAlign: 'center',
        }}
      >
        {label}
      </span>
    </span>,
    tooltip
  )
