/** @jsxImportSource @emotion/react */
import React from 'react'

export interface SitesMapSidePanelProps {
  children: React.ReactNode
  className?: string
  isOpen: boolean
  transitionMs: number
}

export const SitesMapSidePanel = ({
  children,
  className,
  isOpen,
  transitionMs,
}: SitesMapSidePanelProps): JSX.Element => {
  return (
    <div
      className={className}
      css={{
        width: isOpen ? '400px' : 0,
        maxWidth: '50%',
        transition: `width ${transitionMs}ms`,
        position: 'relative',
        overflow: 'hidden',
        border: '1px solid lightgrey',
        boxSizing: 'border-box',
      }}
    >
      {children}
    </div>
  )
}
