/** @jsxImportSource @emotion/react */
import React from 'react'

export interface TitleProps {
  children: React.ReactNode
  className?: string
}

export const Title = ({ children, className }: TitleProps): JSX.Element => {
  return (
    <div className={className} css={{ fontWeight: '700', fontSize: '125%' }}>
      {children}
    </div>
  )
}

const Subtitle = ({ children }: TitleProps): JSX.Element => {
  return <span css={{ fontWeight: '300' }}>{children}</span>
}

Title.Subtitle = Subtitle
