import * as turf from '@turf/turf'
import { sortBy } from 'lodash'
import { Point, Site } from '../../types'

export const getDistance = (from: Point, to: Point): number =>
  turf.distance(turf.point([from.longitude, from.latitude]), turf.point([to.longitude, to.latitude]))

export const getSortedSitesDistances = (
  sites: Site[],
  target: Point,
  maxCoordinateDiff?: number
): { site: Site; distance: number }[] => {
  const prefilteredSites = maxCoordinateDiff
    ? sites.filter(
        (site) =>
          Math.abs(site.latitude - target.latitude) < maxCoordinateDiff &&
          Math.abs(site.longitude - target.longitude) < maxCoordinateDiff
      )
    : sites
  return sortBy(
    prefilteredSites.map((site) => ({
      site,
      distance: getDistance(site, target),
    })),
    (result) => result.distance
  )
}
