import MapboxGL from 'mapbox-gl'
import { FeatureCollection } from 'geojson'
import { SITES_LAYER_ID } from '../useSitesMapController'
export const SOURCE_PREFIX = 'BGS_'
export const LAYER_PREFIX = 'BGL_'

import basicStylesJson from './basicStyles.json'
import satStylesJson from './satStyles.json'
import topoStylesJson from './topoStyles.json'
import sitesLayerJson from './sites.json'

import basicThumb from './basicThumb.png'
import satThumb from './satThumb.jpg'
import topoThumb from './topoThumb.png'

export const sitesLayer = sitesLayerJson as MapboxGL.CircleLayer

export type MapStyleKey = keyof typeof mapStyles

export const mapStyles = {
  basic: (data: FeatureCollection): MapboxGL.Style => createNewStyle(basicStylesJson as MapboxGL.Style, data),
  sat: (data: FeatureCollection): MapboxGL.Style => createNewStyle(satStylesJson as MapboxGL.Style, data),
  topo: (data: FeatureCollection): MapboxGL.Style => createNewStyle(topoStylesJson as MapboxGL.Style, data),
}

export const mapStylesThumbs = {
  basic: basicThumb,
  sat: satThumb,
  topo: topoThumb,
}

/**
 * This is just stripping out all the useful information from a direct export from Mapbox
 * We prefix the IDs too so that we can find them easier for map shuffling
 * @param sourcePrefix
 * @param rawStyle
 * @returns
 */
export function createNewStyle(rawStyle: MapboxGL.Style, sitesData: FeatureCollection): MapboxGL.Style {
  if (!rawStyle.sources) return { sources: {}, layers: [], version: 8 }
  const rawSources = rawStyle.sources || {}
  const rawLayers = rawStyle.layers || []
  const glyphs = rawStyle.glyphs
  const sprite = rawStyle.sprite
  const sourceLookup: Record<string, string> = {}
  const sources = Object.keys(rawStyle.sources).reduce<MapboxGL.Sources>(
    (acc, sKey) => {
      const newId = `${SOURCE_PREFIX}_${sKey}`
      const source = rawSources[sKey]
      // Add to the lookup so we can find this in the layers later
      sourceLookup[sKey] = newId
      return {
        ...acc,
        [newId]: source,
      }
    },
    {
      [SITES_LAYER_ID]: {
        type: 'geojson',
        data: sitesData,
      },
    }
  )
  const layers = [
    ...rawLayers.map<MapboxGL.AnyLayer>((rawLayer) => {
      const newLyr = {
        ...rawLayer,
        id: `${LAYER_PREFIX}_${rawLayer.id}`,
      }
      // IF the source is specified (and it is not a complex type) then go get it from the lookup
      if (
        newLyr.type !== 'background' &&
        newLyr.type !== 'circle' &&
        newLyr.type !== 'custom' &&
        newLyr.source &&
        typeof newLyr.type === 'string'
      )
        newLyr.source = sourceLookup[newLyr.source.toString()]
      return newLyr
    }),
    {
      ...sitesLayerJson,
      id: SITES_LAYER_ID,
      source: SITES_LAYER_ID,
    } as MapboxGL.CircleLayer,
  ]
  return { sources, layers, glyphs, sprite, version: 8 }
}
