/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import ReactMapGL, { NavigationControl } from 'react-map-gl'
import ZoomOutMapIcon from '@mui/icons-material/ZoomOutMap'
import MuiDialog from '@mui/material/Dialog'
import MuiDialogTitle from '@mui/material/DialogTitle'
import MuiDialogContent from '@mui/material/DialogContent'
import MuiDialogActions from '@mui/material/DialogActions'
import MuiButton from '@mui/material/Button'
import 'mapbox-gl/dist/mapbox-gl.css'

import { mapStyles, MapStyleKey } from './styles'

import { SitesMapController, SITES_LAYER_ID, MAX_ZOOM } from './useSitesMapController'
import { atom, useRecoilState } from 'recoil'
import { StyleControl } from './StyleControl'

export interface SitesMapProps {
  controller: SitesMapController
  minimal?: boolean
  children?: React.ReactNode
}

const { REACT_APP_MAPBOX_TOKEN } = process.env

export const mapStyleKeyAtom = atom<MapStyleKey>({
  key: 'SitesMap/mapStyleKeyAtom',
  default: 'basic',
})

export const SitesMap = ({ controller, children, minimal }: SitesMapProps): JSX.Element => {
  const {
    viewport,
    setViewport,
    sitesData,
    handleClick,
    handleHover,
    hoverInfo,
    siteChoicesNode,
    cancelSiteChoices,
    smartFitAll,
  } = controller
  const [mapStyleKey, setMapStyleKey] = useRecoilState(mapStyleKeyAtom)

  const mapStyle = useMemo(() => mapStyles[mapStyleKey](sitesData), [sitesData, mapStyleKey])

  // RENDER

  if (!REACT_APP_MAPBOX_TOKEN)
    return (
      <div>
        No Mapbox token found. Add <code>REACT_APP_MAPBOX_TOKEN</code> to relevant <code>.env</code> file.
      </div>
    )

  return (
    <>
      <div css={{ height: '100%', width: '100%', position: 'relative' }}>
        <ReactMapGL
          onViewportChange={setViewport}
          mapboxApiAccessToken={REACT_APP_MAPBOX_TOKEN}
          onHover={handleHover}
          interactiveLayerIds={[SITES_LAYER_ID]}
          onClick={handleClick}
          maxZoom={MAX_ZOOM}
          mapStyle={mapStyle}
          {...viewport} // must be before width and height
          width="100%"
          height="100%"
        >
          <NavigationControl
            style={{
              right: 10,
              top: 10,
            }}
            showCompass={false}
          />
          {children}
          {hoverInfo && (
            <div
              css={{
                position: 'absolute',
                left: hoverInfo.x,
                top: hoverInfo.y - 10,
                pointerEvents: 'none',
                transform: 'translate(-50%,-100%)',
              }}
            >
              <div>{hoverInfo.node}</div>
            </div>
          )}
        </ReactMapGL>
        <div css={{ position: 'absolute', top: 80, right: 10 }}>
          <div className="mapboxgl-ctrl mapboxgl-ctrl-group">
            <button className="mapboxgl-ctrl-icon" type="button" title="Zoom to Fit" onClick={() => smartFitAll()}>
              <ZoomOutMapIcon sx={{ height: 18, width: 18, paddingTop: '4px' }} />
            </button>
          </div>
        </div>
        {!minimal && (
          <div css={{ position: 'absolute', bottom: 30, left: 8 }}>
            <StyleControl value={mapStyleKey} onChange={setMapStyleKey} />
          </div>
        )}
      </div>
      <MuiDialog open={!!siteChoicesNode} onClose={cancelSiteChoices}>
        <MuiDialogTitle>Select Site</MuiDialogTitle>
        <MuiDialogContent>{siteChoicesNode}</MuiDialogContent>
        <MuiDialogActions>
          <MuiButton onClick={cancelSiteChoices}>Cancel</MuiButton>
        </MuiDialogActions>
      </MuiDialog>
    </>
  )
}
