import React from 'react'
import { useRecoilValue } from 'recoil'

import { selectSampleById } from '../../data/selectors'
import { SampleDetail as Component } from './SampleDetail'
import { Fallback } from './Fallback'
import { withSuspense } from '../withSuspense'

export interface SampleDetailProps {
  sampleId: string | number
}

export const SampleDetail = withSuspense(({ sampleId }: SampleDetailProps): JSX.Element => {
  const sample = useRecoilValue(selectSampleById(Number(sampleId)))

  if (sample === null) return <h2>Sample {sampleId} not found.</h2>

  return <Component sample={sample} />
}, Fallback)
