/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import Skeleton from '@mui/material/Skeleton'

import { selectMetrics } from '../../data/selectors'
import { ReadOnlyField } from '../ReadOnlyField'
import { withSuspense } from '../withSuspense'
import { AutocompleteArrayField, AutocompleteArrayFieldProps } from '../AutocompleteArrayField'

export type MetricsFieldProps = Omit<AutocompleteArrayFieldProps, 'options'>

export const MetricsField = withSuspense<MetricsFieldProps>(
  (props): JSX.Element => {
    const metrics = useRecoilValue(selectMetrics)
    const options = metrics.map(({ id: value, abbreviation: label }) => ({ value, label }))

    return <AutocompleteArrayField {...props} options={options} />
  },
  (props) => <ReadOnlyField {...props} value={<Skeleton />} />
)
