/** @jsxImportSource @emotion/react */
import React from 'react'
import { selector, useRecoilValue } from 'recoil'
import MuiButton from '@mui/material/Button'

import { useUpdatableState } from '../../lib/useUpdatableState'
import { TextField } from '../TextField'
import { CheckboxField } from '../CheckboxField'
import { AutocompleteField } from '../AutocompleteField'
import {
  selectDefaultOrganizationTypeId,
  selectMyUser,
  selectOrganizations,
  selectRoles,
  selectSystemAdminRoleLevel,
  selectUsCountryId,
  selectUsStates,
  useInvalidate,
  useDeferredInvalidate,
} from '../../data/selectors'
import { Organization, User } from '../../types'
import { GrantAction, NAMCResource } from '@namcbugdb/common'
import { RolesField } from './RolesField'
import {
  createOrganizationWithUser,
  deleteOrganization,
  updateOrganization,
  updateUser,
  updateUserRoles,
} from '../../data/calls'
import { useApi } from '../../data/api'
import { useNotifications } from '../AppNotifications'
import { ConfirmNavigateAway } from '../AppRouter'
import { Title } from '../Title'

export interface UserDetailProps {
  user?: User
  organization?: Organization
  isSelf?: boolean
}

interface Fields {
  firstName: string
  lastName: string
  userPhone: string
  affiliationId: number | null
  deleteOldOrganization: boolean
  roleIds: number[]
  organizationName: string
  address1: string
  address2: string
  city: string
  usStateId: number | null
  zipCode: string
  organizationPhone: string
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'stopped' | 'started' | 'stopping'
  isDirty: boolean
  values: Fields
  lastValues?: Fields
}

const selectOrganizationOptions = selector({
  key: 'UserDetail/selectOrganizationOptions',
  get: async ({ get }) => {
    const organizations = get(selectOrganizations)
    return organizations.map(({ id: value, name: label }) => ({ value, label }))
  },
})

const selectUsStateOptions = selector({
  key: 'UserDetail/selectUsStateOptions',
  get: async ({ get }) => {
    const usStates = get(selectUsStates)
    return usStates.map(({ id: value, abbreviation: label }) => ({ value, label }))
  },
})

const getInitialState = ({ user, organization }: Pick<UserDetailProps, 'user' | 'organization'>): State => ({
  editMode: !user && !organization ? 'started' : 'stopped',
  isDirty: false,
  values: {
    firstName: user?.firstName || '',
    lastName: user?.lastName || '',
    userPhone: user?.phone || '',
    affiliationId: user?.affiliationId || null,
    deleteOldOrganization: true,
    roleIds: user?.roleIds || [],
    organizationName: organization?.name || '',
    address1: organization?.address1 || '',
    address2: organization?.address2 || '',
    city: organization?.city || '',
    usStateId: organization?.usStateId || null,
    zipCode: organization?.zipCode || '',
    organizationPhone: organization?.phone || '',
  },
})

export const UserDetail = ({ user, organization, isSelf }: UserDetailProps): JSX.Element => {
  const api = useApi()
  const invalidate = useInvalidate()
  const deferredInvalidate = useDeferredInvalidate()
  const notifications = useNotifications()
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ user, organization }), [
    user,
    organization,
  ])

  const operatorUser = useRecoilValue(selectMyUser)
  const usCountryId = useRecoilValue(selectUsCountryId)
  const typeId = useRecoilValue(selectDefaultOrganizationTypeId)
  const roles = useRecoilValue(selectRoles)
  const systemAdminRoleLevel = useRecoilValue(selectSystemAdminRoleLevel)

  const { values, editMode, isDirty } = state

  const canUpdateOwnUser = operatorUser.hasPermission(NAMCResource.individuals, GrantAction.updateOwn)
  const canUpdateAnyUser = operatorUser.hasPermission(NAMCResource.individuals, GrantAction.updateAny)
  const isNewUser = !user && !organization
  const isOtherSystemAdmin =
    !isSelf &&
    (user?.roleIds || []).some((roleId) =>
      roles.find(({ id, level }) => roleId === id && level === systemAdminRoleLevel)
    )
  const isEditable = editMode === 'started'
  const isEditingUserFields = isEditable
  const isEditingOrganizationFields = isEditable && isSelf
  const isShowingAdminOnlyControls = !isSelf && canUpdateAnyUser
  const isShowingDeleteOldOrganization =
    isShowingAdminOnlyControls &&
    isEditable &&
    values.affiliationId !== user?.affiliationId &&
    organization?.userCount === 1

  const errors: FieldErrors = (() => {
    const result: FieldErrors = {}
    if (isEditingUserFields) {
      result.firstName = !values.firstName.trim()
      result.lastName = !values.lastName.trim()
    }
    if (isEditingOrganizationFields) {
      result.organizationName = values.organizationName.trim().length < 3
    }

    if (!Object.values(result).some((value) => value)) return undefined
    return result
  })()

  const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
    updateState((draft) => {
      Object.assign(draft.values, { [fieldName]: value })
      draft.isDirty = true
    })
  }

  const onEdit = () => {
    updateState((draft) => {
      draft.editMode = 'started'
      draft.lastValues = draft.values
    })
  }

  const onCancel = () => {
    updateState((draft) => {
      if (!draft.lastValues) throw new Error('no values to revert to')
      draft.editMode = 'stopped'
      draft.values = draft.lastValues
      draft.isDirty = false
    })
  }

  const onSave = async () => {
    try {
      updateState((draft) => {
        draft.editMode = 'stopping'
      })
      if (isNewUser) {
        // new user
        await createOrganizationWithUser(
          api,
          invalidate
        )({
          organization: {
            name: values.organizationName,
            typeId,
            address1: values.address1,
            address2: values.address2,
            city: values.city,
            countryId: usCountryId,
            phone: values.organizationPhone,
            usStateId: values.usStateId,
            zipCode: values.zipCode,
          },
          user: {
            firstName: values.firstName,
            lastName: values.lastName,
            phone: values.userPhone,
          },
        })
        // page automatically refreshes here
      } else {
        // existing user
        if (isEditingOrganizationFields) {
          // TODO: only if field changes
          if (!organization?.id) throw new Error('no organization ID')
          if (!organization?.countryId) throw new Error('no organization country ID')
          if (!organization?.typeId) throw new Error('no organization type ID')
          await updateOrganization(
            api,
            deferredInvalidate
          )({
            id: organization.id,
            name: values.organizationName,
            address1: values.address1,
            address2: values.address2,
            city: values.city,
            zipCode: values.zipCode,
            usStateId: values.usStateId,
            countryId: organization.countryId,
            phone: values.organizationPhone,
            typeId: organization.typeId,
          })
        }
        if (isEditingUserFields) {
          // TODO: only if field changes
          if (!user?.id) throw new Error('no user ID')
          const isAffiliationChanged = user.affiliationId !== values.affiliationId
          await updateUser(api, isAffiliationChanged ? invalidate : deferredInvalidate)(
            {
              id: user.id,
              firstName: values.firstName,
              lastName: values.lastName,
              affiliationId: values.affiliationId,
              phone: values.userPhone,
            },
            !!isSelf
          )
        }
        if (isShowingAdminOnlyControls) {
          // TODO: only if roles changes
          if (!user?.id) throw new Error('no user ID')
          await updateUserRoles(
            api,
            deferredInvalidate
          )({
            id: user.id,
            roleIds: values.roleIds,
          })
        }
        if (isShowingDeleteOldOrganization && values.deleteOldOrganization) {
          try {
            await deleteOrganization(api, invalidate)(organization)
          } catch (err) {
            notifications.error(`Failed to delete organization: ${err}`)
          }
        }
      }
      updateState((draft) => {
        draft.editMode = 'stopped'
        draft.isDirty = false
      })
    } catch (err) {
      notifications.error(`Failed to save: ${err}`)
      updateState((draft) => {
        draft.editMode = 'started'
      })
    }
  }

  return (
    <>
      <div css={{ maxWidth: '40rem' }}>
        <div css={{ display: 'flex', gap: '0.5em', alignItems: 'center' }}>
          <Title css={{ marginRight: 'auto' }}>
            {isNewUser ? 'Create New User' : user?.email || <em>no email address</em>}
          </Title>
          {editMode !== 'stopped' && !isNewUser && (
            <MuiButton variant="outlined" onClick={onCancel} disabled={editMode === 'stopping'}>
              Cancel
            </MuiButton>
          )}
          {editMode !== 'stopped' && (
            <MuiButton variant="contained" onClick={onSave} disabled={!!errors || editMode === 'stopping' || !isDirty}>
              Save
            </MuiButton>
          )}
          {editMode === 'stopped' && ((isSelf && canUpdateOwnUser) || canUpdateAnyUser) && (
            <MuiButton variant="outlined" onClick={onEdit} disabled={isOtherSystemAdmin}>
              Edit
            </MuiButton>
          )}
        </div>
        <h4 css={{ margin: '0.75em 0' }}>User</h4>
        <div
          css={{
            display: 'grid',
            gap: '0.75em',
            gridTemplateColumns: '1fr 1fr',
            gridTemplateAreas: `
              "firstName  lastName"
              "userPhone  ."
          `,
          }}
        >
          <TextField
            gridArea="firstName"
            value={values.firstName}
            onChange={(value) => setFieldValue('firstName', value)}
            label="First Name"
            error={errors?.firstName}
            readOnly={!isEditingUserFields}
            maxLength={255}
          />
          <TextField
            gridArea="lastName"
            value={values.lastName}
            onChange={(value) => setFieldValue('lastName', value)}
            label="Last Name"
            error={errors?.lastName}
            readOnly={!isEditingUserFields}
            maxLength={255}
          />
          <TextField
            gridArea="userPhone"
            value={values.userPhone}
            onChange={(value) => setFieldValue('userPhone', value)}
            label="Phone"
            error={errors?.userPhone}
            readOnly={!isEditingUserFields}
            maxLength={50}
          />
        </div>
        {isShowingAdminOnlyControls && (
          <>
            <h4 css={{ margin: '0.75em 0' }}>Roles and Affiliation</h4>
            <div
              css={{
                display: 'flex',
                flexDirection: 'column',
                gap: '0.75em',
              }}
            >
              <AutocompleteField
                label="Belongs To"
                gridArea="affiliationId"
                optionsSelector={selectOrganizationOptions}
                valueLabel={user?.organizationName || ''}
                value={values.affiliationId}
                onChange={(value) => setFieldValue('affiliationId', value)}
                error={errors?.affiliationId}
                readOnly={!isEditable}
              />
              {isShowingDeleteOldOrganization && (
                <CheckboxField
                  label={`Delete "${user?.organizationName}" organization with no remaining users and move any orphaned resources to the selected organization`}
                  gridArea="deleteOldOrganization"
                  value={values.deleteOldOrganization}
                  onChange={(value) => setFieldValue('deleteOldOrganization', value)}
                />
              )}
              <RolesField
                label="Roles"
                gridArea="roleIds"
                value={values.roleIds}
                onChange={(value) => setFieldValue('roleIds', value)}
                error={errors?.roleIds}
                readOnly={!isEditable}
                hideRoleLevel={isOtherSystemAdmin ? undefined : systemAdminRoleLevel}
              />
            </div>
          </>
        )}
        <h4 css={{ margin: '0.75em 0' }}>Organization</h4>
        <div
          css={{
            display: 'grid',
            gap: '0.75em',
            gridTemplateColumns: '1fr 1fr',
            gridTemplateAreas: `
                        "organizationName   organizationName"
                        "address1           address1"
                        "address2           address2"
                        "city               city"
                        "usStateId          zipCode"
                        "organizationPhone  ."
                      `,
          }}
        >
          <TextField
            gridArea="organizationName"
            value={values.organizationName}
            onChange={(value) => setFieldValue('organizationName', value)}
            label="Organization Name"
            error={errors?.organizationName}
            readOnly={!isEditingOrganizationFields}
            maxLength={255}
          />
          <TextField
            gridArea="address1"
            value={values.address1}
            onChange={(value) => setFieldValue('address1', value)}
            label="Address 1"
            error={errors?.address1}
            readOnly={!isEditingOrganizationFields}
            maxLength={255}
          />
          <TextField
            gridArea="address2"
            value={values.address2}
            onChange={(value) => setFieldValue('address2', value)}
            label="Address 2"
            error={errors?.address2}
            readOnly={!isEditingOrganizationFields}
            maxLength={255}
          />
          <TextField
            gridArea="city"
            value={values.city}
            onChange={(value) => setFieldValue('city', value)}
            label="City"
            error={errors?.city}
            readOnly={!isEditingOrganizationFields}
            maxLength={255}
          />
          <AutocompleteField
            gridArea="usStateId"
            value={values.usStateId}
            valueLabel="State"
            onChange={(value) => setFieldValue('usStateId', value)}
            optionsSelector={selectUsStateOptions}
            label="State"
            error={errors?.usStateId}
            readOnly={!isEditingOrganizationFields}
            isClearable
          />
          <TextField
            gridArea="zipCode"
            value={values.zipCode}
            onChange={(value) => setFieldValue('zipCode', value)}
            label="Zip"
            error={errors?.zipCode}
            readOnly={!isEditingOrganizationFields}
            maxLength={20}
          />
          <TextField
            gridArea="organizationPhone"
            value={values.organizationPhone}
            onChange={(value) => setFieldValue('organizationPhone', value)}
            label="Phone"
            error={errors?.organizationPhone}
            readOnly={!isEditingOrganizationFields}
            maxLength={50}
          />
        </div>
      </div>
      {isDirty && <ConfirmNavigateAway message="Some changes are not saved. Are you sure you want to leave?" />}
    </>
  )
}
