/** @jsxImportSource @emotion/react */
import React, { useRef } from 'react'
import { useHotkeys } from 'react-hotkeys-hook'

import { TextField, TextFieldProps } from '../TextField'

export interface TextFilterFieldProps extends TextFieldProps {
  hotkeys?: string | false
}

export const TextFilterField = ({
  hotkeys = 'cmd+f, ctrl+f',
  inputRef,
  onChange,
  value,
  ...restProps
}: TextFilterFieldProps): JSX.Element => {
  const ownRef = useRef<HTMLInputElement>(null)
  const effectiveRef = inputRef || ownRef

  useHotkeys(hotkeys || '', (e) => {
    e.preventDefault()
    e.stopPropagation()
    effectiveRef.current?.focus()
  })

  return <TextField css={{ width: '30em' }} value={value} onChange={onChange} inputRef={effectiveRef} {...restProps} />
}
