import gql from 'graphql-tag'
import { PaginatedRecords, pubTypes } from '@namcbugdb/common'

import { Api } from '../../../data/api'
import { Taxon } from '../../../types'
import { deNull } from '../../../lib/deNull'

export const fetchPublicTaxaQuery = gql`
  query ($offset: Int) {
    taxonomies(limit: 10000, offset: $offset) {
      records {
        taxonomyId
        scientificName
        levelId
        synonyms
        parentTaxonomyId
      }
      nextOffset
    }
  }
`

export const fetchPublicTaxa =
  (api: Api) =>
  async (onRecords?: (qty: number) => void): Promise<Taxon[]> => {
    const data = await api.queryAllPages<{
      taxonomies: PaginatedRecords<pubTypes.PubTaxonomy>
    }>(fetchPublicTaxaQuery, {}, { dataProperty: 'taxonomies', onRecords })
    const { records } = data.taxonomies

    return records.map(
      ({ taxonomyId: id, scientificName: name, parentTaxonomyId: parentId, synonyms: synonymsJson, ...rest }) =>
        deNull(
          {
            id,
            name,
            levelRankOrder: 0, // reassigned in selector
            parentId,
            synonyms: synonymsJson ? JSON.parse(synonymsJson) : [],
            levelName: '', // in-fill for typing
            notes: '', // in-fill for typing
            isTargetEffort: false, // in-fill for typing
            ...rest,
          },
          {}
        )
    )
  }
