/** @jsxImportSource @emotion/react */
import React from 'react'
import TextField from '@mui/material/TextField'
import MuiAutocomplete from '@mui/material/Autocomplete'
import MuiChip, { ChipProps } from '@mui/material/Chip'

import { ReadOnlyField } from '../ReadOnlyField'
import { CommonFieldProps } from '../../types'

export interface Option {
  value: string | number
  label: string
  disabled?: boolean
}

export interface AutocompleteArrayFieldProps extends CommonFieldProps<(string | number)[]> {
  options: Option[]
}

const SelectedOptionChip = ({ label, value, onDelete }: Option & ChipProps) => (
  <MuiChip label={label} key={value} onDelete={onDelete} css={{ margin: '2px' }} />
)

export const AutocompleteArrayField = ({
  // CommonFieldProps
  className,
  disabled,
  error,
  gridArea,
  label,
  onChange,
  readOnly,
  value: valueArray,

  // AutocompleteArrayField
  options,
}: AutocompleteArrayFieldProps): JSX.Element => {
  const selectedOptions = options.filter((option) => valueArray.includes(option.value))

  if (readOnly)
    return (
      <ReadOnlyField
        label={label}
        gridArea={gridArea}
        className={className}
        value={
          selectedOptions.length > 0
            ? selectedOptions.map(({ value, label }) => <SelectedOptionChip key={value} value={value} label={label} />)
            : null
        }
      />
    )

  return (
    <MuiAutocomplete
      multiple
      className={className}
      disablePortal
      options={options}
      disabled={disabled}
      getOptionLabel={(option) => option.label}
      getOptionDisabled={(option) => !!option.disabled}
      renderInput={(params) => (
        <TextField {...params} label={label} error={error} InputLabelProps={{ shrink: selectedOptions.length > 0 }} />
      )}
      renderTags={(renderedOptions: readonly Option[], getTagProps) =>
        renderedOptions.map(({ value, label }, index) => (
          <SelectedOptionChip value={value} label={label} {...getTagProps({ index })} key={value} />
        ))
      }
      onChange={(event, selected) => {
        onChange(selected.map(({ value }) => value))
      }}
      size="small"
      value={selectedOptions}
      css={{ gridArea }}
      disableClearable
    />
  )
}
