import { get } from 'lodash'

import { GeoJsonPolygonCoordinates } from '../../types'

const isValidGeoJsonPolygonCoordinates = (object: unknown): object is GeoJsonPolygonCoordinates => {
  if (!Array.isArray(object) || object.length === 0) throw new Error('Polygon is not an array of rings')
  object.forEach((ring) => {
    if (!Array.isArray(ring) || ring.length < 4) throw new Error('Polygon ring does not contain not enough points')
    ring.forEach((lngLat) => {
      if (!Array.isArray(lngLat) || lngLat.length !== 2) throw new Error('Polygon has invalid coordinates')
      if (typeof lngLat[0] !== 'number') throw new Error('Polygon has invalid coordinates')
      if (typeof lngLat[1] !== 'number') throw new Error('Polygon has invalid coordinates')
    })
    const lastPointIndex = ring.length - 1
    if (ring[0][0] !== ring[lastPointIndex][0] || ring[0][1] !== ring[lastPointIndex][1])
      throw new Error('Polygon is not a closed ring')
  })

  return true
}

export const parseGeoJsonPolygon = (json: string): GeoJsonPolygonCoordinates => {
  let object: unknown
  let extracted: unknown

  try {
    object = JSON.parse(json)
  } catch {
    throw new Error('not valid JSON')
  }

  if (Array.isArray(object)) {
    extracted = object
  } else {
    const type = get(object, 'type')
    switch (type) {
      case 'FeatureCollection': {
        const features = get(object, 'features')
        if (!Array.isArray(features) || features.length !== 1)
          throw new Error('FeatureCollection must have exactly one Feature')
        if (get(features[0], 'geometry.type') !== 'Polygon') throw new Error('Feature must be a Polygon')
        extracted = get(features[0], 'geometry.coordinates')
        break
      }
      case 'Feature': {
        if (get(object, 'geometry.type') !== 'Polygon') throw new Error('Feature must be a Polygon')
        extracted = get(object, 'geometry.coordinates')
        break
      }
      case 'Polygon': {
        extracted = get(object, 'coordinates')
        break
      }
      case undefined:
        throw new Error('not valid GeoJSON, missing type')
      default:
        throw new Error('not valid GeoJSON, invalid type')
    }
  }

  if (!extracted || !isValidGeoJsonPolygonCoordinates(extracted)) throw new Error('could not parse coordinates')

  return extracted
}
