/** @jsxImportSource @emotion/react */
import React from 'react'

import { Chip } from '../Chip'
import { Filter } from './types'

const NoneChip = () => <Chip label="No filters" />

export interface FilterChipsProps<T> {
  className?: string
  filters: Filter<T>[]
  joinLabel?: 'and' | 'or'
  noneNode?: React.ReactNode
}

enum CHIP_COLOR {
  default = 'lightblue',
  warn = '#ffcc00',
  error = '#ff4400',
}

export const FilterChips = <T,>({
  className,
  filters,
  joinLabel,
  noneNode = <NoneChip />,
}: FilterChipsProps<T>): JSX.Element => {
  return (
    <div
      className={className}
      css={{ flex: 1, display: 'flex', flexWrap: 'wrap', gap: '0.5em', alignItems: 'center', fontSize: '0.8125rem' }}
    >
      {filters.length > 0
        ? filters.map(({ id, label, textColor, chipColor = CHIP_COLOR.default, onDelete }, index) => (
            <React.Fragment key={id}>
              {joinLabel && !!index && <span css={{ fontStyle: 'italic', opacity: 0.6 }}> {joinLabel} </span>}
              <Chip
                label={label}
                textColor={textColor}
                chipColor={CHIP_COLOR[chipColor] || chipColor}
                onDelete={onDelete}
              />
            </React.Fragment>
          ))
        : noneNode}
    </div>
  )
}
