import Excel, { Workbook } from 'exceljs'

interface AddSingleTableWorksheetConfig<T> {
  wsName: string
  columns: Partial<Excel.Column>[]
  rows: T[]
}

export const createSingleTableWorkbook = <T>(params: AddSingleTableWorksheetConfig<T>): Workbook => {
  const wb = new Excel.Workbook()
  addSingleTableWorksheet(wb, params)
  return wb
}

export const addSingleTableWorksheet = <T>(
  wb: Excel.Workbook,
  { wsName, columns, rows }: AddSingleTableWorksheetConfig<T>
): void => {
  const ws = wb.addWorksheet(wsName)

  // add data
  ws.columns = columns as Partial<Excel.Column>[]
  ws.addRows(rows)

  // autofit columns
  ws.columns.forEach((column) => {
    const columnValues = column.values
    if (!columnValues) return
    const lengths = columnValues.slice(1).map((value) => (value || '').toString().length)
    const maxLength = Math.max(...lengths.filter((v) => typeof v === 'number'))
    column.width = maxLength + 2 // add some padding for filter button
  })

  // freeze rows
  ws.views = [{ state: 'frozen', xSplit: undefined, ySplit: 1, activeCell: 'A1' }]
}
