/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiTextField, { StandardTextFieldProps } from '@mui/material/TextField'

import { ReadOnlyField } from '../ReadOnlyField'
import { CommonFieldProps } from '../../types'

interface NumberFieldProps extends CommonFieldProps<number | null>, Omit<StandardTextFieldProps, 'value' | 'onChange'> {
  inputRef?: React.Ref<HTMLInputElement>
  max?: number
  min?: number
  precision?: number
  step?: number
}

export const NumberField = ({
  // CommonFieldProps
  className,
  disabled,
  error,
  gridArea,
  label,
  onChange,
  readOnly,
  value,

  // NumberField
  inputRef,
  max,
  min,
  precision,
  step,

  // StandardTextFieldProps
  inputProps = {},
  ...restProps
}: NumberFieldProps): JSX.Element => {
  if (readOnly) return <ReadOnlyField className={className} gridArea={gridArea} label={label} value={value} />

  const handleChange = ({ target: { value: textValue } }) => {
    if (!textValue.trim()) {
      onChange(null)
      return
    }
    const numberValue = Number(textValue)
    if (precision !== undefined && Number(numberValue.toFixed(precision)) !== numberValue) return
    if (min !== undefined && numberValue < min) return
    if (max !== undefined && numberValue > max) return
    onChange(numberValue)
  }

  const combinedInputProps = { ...inputProps, step }

  return (
    <MuiTextField
      size="small"
      className={className}
      disabled={disabled || readOnly}
      error={error}
      inputRef={inputRef}
      label={label}
      onChange={handleChange}
      value={value ?? ''}
      type="number"
      css={{ gridArea }}
      inputProps={combinedInputProps}
      {...restProps}
    />
  )
}
