/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiSkeleton from '@mui/material/Skeleton'
import { ReadOnlyField } from '../ReadOnlyField'

import { withSuspense } from '../withSuspense'
import { TaxonomySelectField } from '../TaxonomyField'
import { useRecoilValue } from 'recoil'
import {
  selectPublicMethodOptions,
  selectPublicProjectOptions,
  selectPublicTaxa,
  selectPublicTypeOptions,
  selectPublicUsStateOptions,
} from './selectors'
import { CriterionAutocomplete, CriterionFieldProps } from '../CriterionAutocomplete'

export const ProjectCriterionField = withSuspense(
  (props: CriterionFieldProps<number>): JSX.Element => {
    const options = useRecoilValue(selectPublicProjectOptions)
    return <CriterionAutocomplete {...props} options={options} label="Project" />
  },
  ({ gridArea }) => <ReadOnlyField css={{ gridArea }} value={<MuiSkeleton />} label="US State" />
)

