/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'

import { selectSitesBySingleSiteId } from '../../data/selectors'
import { ReadOnlyField } from '../ReadOnlyField'
import { withSuspense } from '../withSuspense'
import { ReadOnlyFieldProps } from '../ReadOnlyField/ReadOnlyField'

export const WaterbodyNameField = withSuspense(
  ({ siteId, ...props }: Omit<ReadOnlyFieldProps, 'value'> & { siteId: number }) => {
    const sites = useRecoilValue(selectSitesBySingleSiteId(siteId))
    return <ReadOnlyField {...props} value={sites[0].waterbodyName} />
  },
  (props) => <ReadOnlyField {...props} value={<MuiSkeleton />} />
)
