const plurals = {
  is: 'are',
  box: 'boxes',
  taxon: 'taxa',
  child: 'children',
  taxa: false,
  'individuals counted': false,
}

export const pluralize = (qty: number | null | undefined, word: string): string => {
  if (typeof qty === 'number' && qty === 1) return word
  const plural = plurals[word]
  if (plural === false) return word
  return plural ?? `${word}s`
}
