import { ASTNode } from 'graphql'
import log from 'loglevel'

import { Api, LogLevel } from './Api'

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export type MockResponderTuples = [ASTNode, (...args: any[]) => unknown][]

const nextRequestNumber = (() => {
  let requestNumber = 1
  return () => String(requestNumber++)
})()

export class MockApi implements Api {
  responseDelay = 0
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  responders = new Map<ASTNode, (...args: any[]) => unknown>()

  constructor(responders?: MockResponderTuples) {
    if (responders) this.setResponders(responders)
  }

  setResponders = (responders: MockResponderTuples): void => {
    this.responders.clear()
    this.addResponders(responders)
  }

  addResponders = (responders: MockResponderTuples): void => {
    responders.forEach(([queryOrMutation, responder]) => {
      this.responders.set(queryOrMutation, responder)
    })
  }

  setResponseDelay = (ms: number): void => {
    this.responseDelay = ms
  }

  wait = async (): Promise<void> => {
    return new Promise((resolve) => {
      window.setTimeout(resolve, this.responseDelay)
    })
  }

  signOut = (): void => {
    log.debug('MockApi signOut')
  }

  query = async <T>(query: ASTNode, variables?: { [key: string]: unknown }): Promise<T> => {
    const requestNumber = nextRequestNumber()
    log.debug(
      `%cMockApi query #${requestNumber}`,
      'color: black; background: lightgreen',
      query.loc?.source.body,
      variables
    )
    const responder = this.responders.get(query)
    await this.wait()
    if (responder) {
      const response = responder(variables)
      log.debug(`%cMockApi responded to query #${requestNumber}`, 'color: black; background: #0cb700', response)
      return response as T
    }
    return new Promise(() => {
      log.debug(`%cMockApi not responding to query #${requestNumber}`, 'color: black; background: lightgray')
    })
  }

  queryAllPages = async <T>(
    query: ASTNode,
    variables: { [key: string]: unknown },
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    { dataProperty }: { dataProperty: string }
  ): Promise<T> => {
    const requestNumber = nextRequestNumber()
    log.debug(
      `%cMockApi queryAllPages #${requestNumber}`,
      'color: black; background: lightgreen',
      query.loc?.source.body,
      variables
    )
    const responder = this.responders.get(query)
    await this.wait()
    if (responder) {
      const response = responder(variables)
      log.debug(`%cMockApi responded to queryAllPages #${requestNumber}`, 'color: black; background: #0cb700', response)
      return response as T
    }
    return new Promise(() => {
      log.debug(`%cMockApi not responding to queryAllPages #${requestNumber}`, 'color: black; background: lightgray')
    })
  }

  mutation = async <T>(query: ASTNode, variables?: { [key: string]: unknown }): Promise<T> => {
    const requestNumber = nextRequestNumber()
    log.debug(
      `%cMockApi mutation #${requestNumber}`,
      'color: black; background: #ffd17a',
      query.loc?.source.body,
      variables
    )
    const responder = this.responders.get(query)
    await this.wait()
    if (responder) {
      const response = responder(variables)
      log.debug(`%cMockApi responded to mutation #${requestNumber}`, 'color: black; background: orange', response)
      return response as T
    }
    return new Promise(() => {
      log.debug(`%cMockApi not responding to mutation #${requestNumber}`, 'color: black; background: lightgray')
    })
  }

  log = async (level: LogLevel, message: string, context: Record<string, unknown>): Promise<void> => {
    log.debug(`%cMockApi log ${level}`, 'color: black; background: #ff70ee', message, context)
  }
}
