import React, { useState, useCallback, useRef, useEffect } from 'react'
import produce, { Draft } from 'immer'

export type Updater<T> = (draft: Draft<T>) => void

export type UpdatableStateReturnTuple<T> = [
  state: T,
  setState: React.Dispatch<React.SetStateAction<T>>,
  updateState: (updater: Updater<T>) => void
]

export const useUpdatableState = <T>(initialState: T, deps: React.DependencyList): UpdatableStateReturnTuple<T> => {
  const [state, setState] = useState<T>(initialState)
  const instance = useRef<{ isFirstRender: boolean }>({ isFirstRender: true }).current
  const updateState = useCallback((updater: Updater<T>) => setState((current) => produce(current, updater)), [setState])

  useEffect(() => {
    if (instance.isFirstRender) {
      instance.isFirstRender = false
      return
    }
    setState(initialState)
  }, deps)

  return [state, setState, updateState]
}
