import { useCallback } from 'react'

import { useDebounce } from '../../lib/useDebounce'

type UseTextFilterFieldReturnTuple = [
  text: string,
  setText: (newValue: string) => void,
  filterText: string,
  clear: () => void
]

export const useTextFilterField = (
  state: [text: string, setText: (newValue: string) => void]
): UseTextFilterFieldReturnTuple => {
  const [text, setText] = state

  const [rawFilterText, setRawFilterText] = useDebounce(text, 500)

  const filterText = rawFilterText.trim()

  const clear = useCallback(() => {
    setText('')
    setRawFilterText('')
  }, [])

  return [text, setText, filterText, clear]
}
