import dayjs from 'dayjs'
import utc from 'dayjs/plugin/utc'
import timezone from 'dayjs/plugin/timezone' // depends on utc

dayjs.extend(utc)
dayjs.extend(timezone)

const APP_TIME_ZONE = 'US/Mountain'

export const formatNumber = (value: number): string => value.toLocaleString()

export const formatUtcDateTimeToAppDate = (utcDateTime: string | null): string | null => {
  if (!utcDateTime) return null
  if (utcDateTime.match(/T00:00:00(\+00:00|Z)/)) return utcDateTime.substring(0, 'YYYY-MM-DD'.length)
  return dayjs(utcDateTime).tz(APP_TIME_ZONE).format('YYYY-MM-DD')
}

export const getCurrentDateTime = (): string => {
  return dayjs().toISOString()
}
