import React from 'react'
import { useRecoilValue } from 'recoil'

import { selectOrganismsBySampleId, selectSampleById } from '../../data/selectors'
import { SampleTaxonomyIdentification as Component } from './SampleTaxonomyIdentification'
import { Fallback } from './Fallback'
import { withSuspense } from '../withSuspense'

export interface SampleTaxonomyIdentificationProps {
  sampleId: string | number
}

export const SampleTaxonomyIdentification = withSuspense(
  ({ sampleId }: SampleTaxonomyIdentificationProps): JSX.Element => {
    const sample = useRecoilValue(selectSampleById(Number(sampleId)))
    const sampleOrganisms = useRecoilValue(selectOrganismsBySampleId(Number(sampleId)))

    if (sample === null) return <h2>Sample {sampleId} not found.</h2>

    return <Component sample={sample} sampleOrganisms={sampleOrganisms} />
  },
  Fallback
)
