import React, { Suspense } from 'react'

export const withSuspense = <T,>(Component: React.FC<T>, Fallback: React.FC<T>): React.FC<T> => {
  const WrappedComponent: React.FC<T> = (props: T): JSX.Element => (
    <Suspense fallback={<Fallback {...props} />}>
      <Component {...props} />
    </Suspense>
  )
  WrappedComponent.displayName = Component.displayName
  return WrappedComponent
}
