import { PaginatedRecords, pubTypes } from '@namcbugdb/common'
import gql from 'graphql-tag'

import { Api } from '../../../data/api'

export const fetchPublicProjectsQuery = gql`
  query ($offset: Int!) {
    projects(limit: 10000, offset: $offset) {
      records {
        projectId
        projectName
      }
      nextOffset
    }
  }
`

export const fetchPublicProjects =
  (api: Api) =>
  async (onRecords?: (qty: number) => void): Promise<pubTypes.PubProject[]> => {
    const data = await api.queryAllPages<{ projects: PaginatedRecords<pubTypes.PubProject> }>(
      fetchPublicProjectsQuery,
      {},
      { dataProperty: 'projects', onRecords }
    )

    return data.projects.records
  }
