/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { useSnackbar } from 'notistack'
import IconButton from '@mui/material/IconButton'
import CloseIcon from '@mui/icons-material/Close'

import { useApi } from '../../data/api'

interface Notifications {
  info: (message: string, deferMs?: number) => () => void
  error: (message: string) => void
}

export const useNotifications = (): Notifications => {
  const { enqueueSnackbar, closeSnackbar } = useSnackbar()

  const api = useApi()

  return useMemo(() => {
    const closeAction = (key) => (
      <IconButton size="small" aria-label="close" color="inherit" onClick={() => closeSnackbar(key)}>
        <CloseIcon fontSize="small" />
      </IconButton>
    )

    return {
      info: (message: string, deferMs = 0) => {
        const timeout = window.setTimeout(() => {
          enqueueSnackbar(message, {
            autoHideDuration: 4000,
            action: closeAction,
          })
        }, deferMs)
        return () => {
          clearTimeout(timeout)
        }
      },
      error: (message: string) => {
        enqueueSnackbar(message, { persist: true, variant: 'error', action: closeAction })
        api.log('WARN', `notification/error: "${message}"`, {})
      },
    }
  }, [enqueueSnackbar, closeSnackbar, api])
}
