import { Lookups } from '@namcbugdb/common'
import { sortBy } from 'lodash'
import { atom, selector, selectorFamily } from 'recoil'

import { fetchLookups } from '../../data/calls'
import { Taxon } from '../../types'
import { CriterionOption } from '../CriterionAutocomplete'
import { fetchPublicProjects } from './calls/fetchPublicProjects'
import { fetchPublicTaxa } from './calls/fetchPublicTaxa'
import { fetchPublicTaxaSiteIdsByTaxonId } from './calls/fetchPublicTaxaSiteIdsByTaxonId'
import { publicApiAtom } from './publicApi'
import { PublicMapSite } from './types'

export const publicSitesAtom = atom<PublicMapSite[]>({
  key: 'publicSitesAtom',
  default: [],
})

export const selectPublicLookups = selector<Lookups>({
  key: 'selectPublicLookups',
  get: async ({ get }) => {
    const publicApi = get(publicApiAtom)
    return fetchLookups(publicApi)()
  },
})

export const selectPublicProjectOptions = selector<CriterionOption<number>[]>({
  key: 'selectPublicProjectOptions',
  get: async ({ get }) => {
    const publicApi = get(publicApiAtom)

    const projects = await fetchPublicProjects(publicApi)()
    return sortBy(projects, (project) => project.projectName).map(({ projectId: id, projectName: name }) => ({
      id,
      optionLabel: name,
      chipLabel: `in project ${name}`,
    }))
  },
})

export const selectPublicUsStateOptions = selector<CriterionOption<number>[]>({
  key: 'selectPublicUsStateOptions',
  get: async ({ get }) => {
    const lookup = get(selectPublicLookups).geo?.states
    if (!lookup) throw new Error('Missing lookup in query')
    return sortBy(lookup, (usState) => usState.name).map(({ id, name }) => ({
      id,
      optionLabel: name,
      chipLabel: `in ${name}`,
    }))
  },
})

export const selectPublicTypeOptions = selector<CriterionOption<number>[]>({
  key: 'selectPublicTypeOptions',
  get: async ({ get }) => {
    const lookup = get(selectPublicLookups).sample?.sampleTypes
    if (!lookup) throw new Error('Missing lookup in query')
    return lookup.map(({ id, name }) => ({
      id,
      optionLabel: name,
      chipLabel: `type is ${name}`,
    }))
  },
})

export const selectPublicMethodOptions = selector<CriterionOption<number>[]>({
  key: 'selectPublicMethodOptions',
  get: async ({ get }) => {
    const lookup = get(selectPublicLookups).sample?.sampleMethods
    if (!lookup) throw new Error('Missing lookup in query')
    return lookup.map(({ id, name }) => ({
      id,
      optionLabel: name,
      chipLabel: `method is ${name}`,
    }))
  },
})

export const selectPublicTaxa = selector<Taxon[]>({
  key: 'selectPublicTaxa',
  get: async ({ get }) => {
    const publicApi = get(publicApiAtom)
    return fetchPublicTaxa(publicApi)()
  },
})

export const selectPublicTaxaSiteIdsByTaxonId = selectorFamily<number[], number>({
  key: 'PublicMap/selectPublicTaxaSiteIdsByTaxonId',
  get:
    (id) =>
    async ({ get }) => {
      const publicApi = get(publicApiAtom)
      return fetchPublicTaxaSiteIdsByTaxonId(publicApi)(id)
    },
})
