/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import Skeleton from '@mui/material/Skeleton'

import { selectPredictors } from '../../data/selectors'
import { ReadOnlyField } from '../ReadOnlyField'
import { withSuspense } from '../withSuspense'
import { AutocompleteArrayField, AutocompleteArrayFieldProps } from '../AutocompleteArrayField'

export type PredictorsFieldProps = Omit<AutocompleteArrayFieldProps, 'options'>

export const PredictorsField = withSuspense<PredictorsFieldProps>(
  (props): JSX.Element => {
    const predictors = useRecoilValue(selectPredictors)
    const options = predictors.map(({ id: value, abbreviation: label }) => ({ value, label }))

    return <AutocompleteArrayField {...props} options={options} />
  },
  (props) => <ReadOnlyField {...props} value={<Skeleton />} />
)
