import React from 'react'
import { useRecoilValue } from 'recoil'

import { selectBoxById, selectMyUser } from '../../data/selectors'
import { withSuspense } from '../withSuspense'
import { BoxDetail as Component } from './BoxDetail'
import { Fallback } from './Fallback'

export interface BoxDetailProps {
  boxId: string | number
}

export const BoxDetail = withSuspense(({ boxId }: BoxDetailProps): JSX.Element => {
  const box = useRecoilValue(selectBoxById(Number(boxId)))
  const user = useRecoilValue(selectMyUser)

  if (box === null) return <h2>Box {boxId} not found.</h2>

  return <Component box={box} user={user} />
}, Fallback)
