/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { useRecoilState, atom, useRecoilValue } from 'recoil'
import { sortBy } from 'lodash'
// import log from 'loglevel'

import { DataGrid, renderers, DataGridRecordCount } from '../DataGrid'
import { User } from '../../types'
import { usersColumnDefs } from './columnDefs'
import { useNavigate } from '../AppRouter'
import { Filter, FilterChips } from '../Filters'
import { withSuspense } from '../withSuspense'
import { Fallback } from './Fallback'
import { selectRoles, selectUsers } from '../../data/selectors'
import { TextFilterField, useTextFilterField } from '../TextFilterField'

export interface UserRow extends User {
  userId: {
    label: string
    onClick: () => void
  }
}

const textFilterState = atom({
  key: 'UsersListing/textFilterState',
  default: '',
})

export const UsersListing = withSuspense((): JSX.Element => {
  const navigate = useNavigate()
  const users = useRecoilValue(selectUsers)
  const roles = useRecoilValue(selectRoles)

  // STATE AND REFS

  const [textFilter, setTextFilter, textFilterDebounced, clearTextFilter] = useTextFilterField(
    useRecoilState(textFilterState)
  )

  // FILTERS

  const filters = useMemo(() => {
    const result: Filter<User>[] = []

    if (textFilterDebounced) {
      const filterText = textFilterDebounced.toLowerCase()
      result.push({
        id: 'text',
        label: `Name/Affiliation contains "${textFilterDebounced}"`,
        filterFn: ({ fullName, organizationName }) =>
          fullName.toLowerCase().includes(filterText) || organizationName.toLowerCase().includes(filterText),
        onDelete: clearTextFilter,
      })
    }

    return result
  }, [textFilterDebounced])

  const rows = useMemo<UserRow[]>(() => {
    const filtered = filters.reduce<User[]>((acc, { filterFn }) => acc.filter(filterFn), users)
    return filtered.map((user) => ({
      ...user,
      userId: {
        label: String(user.id),
        onClick: () => navigate.userDetail({ userId: user.id }),
      },
      roles: sortBy(user.roleIds).map((roleId) => roles.find((role) => role.id === roleId)?.name || 'Unknown'),
    }))
  }, [users, filters])

  // RENDER

  return (
    <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
      <div css={{ display: 'flex', gap: '0.5em' }}>
        <TextFilterField value={textFilter} onChange={setTextFilter} label="Name or Affiliation" />
      </div>
      <div css={{ minHeight: '2em', marginTop: '0.75em', display: 'flex' }}>
        <FilterChips filters={filters} joinLabel="and" />
        <DataGridRecordCount total={users.length} visible={rows.length} descriptor="users" />
      </div>
      <div css={{ flex: 1 }}>
        <DataGrid<UserRow> rows={rows} columnDefs={usersColumnDefs} frameworkComponents={renderers} />
      </div>
    </div>
  )
}, Fallback)
