import gql from 'graphql-tag'
import { PaginatedRecords, pubTypes } from '@namcbugdb/common'

import { Api } from '../../../data/api'
import { PublicMapSite } from '../types'

export const fetchPublicSitesQuery = gql`
  query ($offset: Int) {
    sites(limit: 10000, offset: $offset) {
      records {
        siteId
        siteName
        longitude
        latitude
        usStateId
        methodIds
        typeIds
        projectIds
        years
      }
      nextOffset
    }
  }
`

export const transformPublicSite = ({ siteId: id, siteName: name, ...rest }: pubTypes.PubSite): PublicMapSite => ({
  id,
  name,
  ...rest,
})

export const fetchPublicSites =
  (api: Api) =>
  async (firstOffset: number, lastOffset: number): Promise<PublicMapSite[]> => {
    const data = await api.queryAllPages<{ sites: PaginatedRecords<pubTypes.PubSite> }>(
      fetchPublicSitesQuery,
      {},
      { dataProperty: 'sites', firstOffset, lastOffset }
    )
    return data.sites.records.map(transformPublicSite)
  }
