/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiTextField, { StandardTextFieldProps } from '@mui/material/TextField'

import { ReadOnlyField } from '../ReadOnlyField'
import { CommonFieldProps } from '../../types'

export interface TextFieldProps extends CommonFieldProps<string>, Omit<StandardTextFieldProps, 'value' | 'onChange'> {
  inputRef?: React.RefObject<HTMLInputElement>
  maxLength?: number
}

export const TextField = ({
  // CommonFieldProps
  className,
  disabled,
  error,
  gridArea,
  label,
  onChange,
  readOnly,
  value,

  // TextField
  inputRef,
  maxLength,

  // StandardTextFieldProps
  inputProps = {},
  ...restProps
}: TextFieldProps): JSX.Element => {
  if (readOnly) return <ReadOnlyField className={className} gridArea={gridArea} label={label} value={value} />

  const handleChange = ({ target: { value: newValue } }) => {
    onChange(newValue)
  }

  const combinedInputProps = { ...inputProps, maxLength }

  return (
    <MuiTextField
      className={className}
      disabled={disabled}
      error={error}
      label={label}
      onChange={handleChange}
      value={value}
      size="small"
      inputRef={inputRef}
      css={{ gridArea }}
      inputProps={combinedInputProps}
      {...restProps}
    />
  )
}
