/** @jsxImportSource @emotion/react */
import React from 'react'
import FormControlLabel from '@mui/material/FormControlLabel'
import Checkbox from '@mui/material/Checkbox'

import { ReadOnlyField } from '../ReadOnlyField'
import { CommonFieldProps } from '../../types'

interface CheckboxFieldProps extends Omit<CommonFieldProps<boolean>, 'error' | 'label'> {
  label: string
  valueLabels?: [string, string]
}

export const CheckboxField = ({
  // CommonFieldProps
  className,
  disabled,
  gridArea,
  onChange,
  readOnly,
  value,

  // CheckboxField
  label,
  valueLabels = ['No', 'Yes'],
}: CheckboxFieldProps): JSX.Element => {
  if (readOnly)
    return (
      <ReadOnlyField
        className={className}
        gridArea={gridArea}
        label={label}
        value={value ? valueLabels[1] : valueLabels[0]}
      />
    )

  const handleChange = ({ target: { checked: newValue } }) => {
    onChange(newValue)
  }

  return (
    <FormControlLabel
      className={className}
      disabled={disabled}
      label={label}
      control={<Checkbox onChange={handleChange} checked={value} />}
      css={{ gridArea }}
    />
  )
}
