/** @jsxImportSource @emotion/react */
import React from 'react'
import { Marker } from 'react-map-gl'
import { useUpdatableState } from '../../lib/useUpdatableState'

import { Point } from '../../types'

export interface PinSvgProps {
  color?: string
  largeDot?: boolean
}

export interface PinMarkerProps extends PinSvgProps {
  onDragEnd?: (newPoint: Point) => void
  site: Point
}

export const SITE_PIN_COLOR = '#aa00aa'

export const PinSvg = ({ color = SITE_PIN_COLOR, largeDot }: PinSvgProps): JSX.Element => (
  <svg
    version="1.1"
    xmlns="http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox="50 0 195 295"
    xmlSpace="preserve"
  >
    <path
      style={{ fill: color }}
      d="M146.667,0C94.903,0,52.946,41.957,52.946,93.721c0,22.322,7.849,42.789,20.891,58.878
c4.204,5.178,11.237,13.331,14.903,18.906c21.109,32.069,48.19,78.643,56.082,116.864c1.354,6.527,2.986,6.641,4.743,0.212
c5.629-20.609,20.228-65.639,50.377-112.757c3.595-5.619,10.884-13.483,15.409-18.379c6.554-7.098,12.009-15.224,16.154-24.084
c5.651-12.086,8.882-25.466,8.882-39.629C240.387,41.962,198.43,0,146.667,0z M146.667,144.358
c-28.892,0-52.313-23.421-52.313-52.313c0-28.887,23.421-52.307,52.313-52.307s52.313,23.421,52.313,52.307
C198.98,120.938,175.559,144.358,146.667,144.358z"
    />
    <circle style={{ fill: color }} cx="146.667" cy="90.196" r={largeDot ? '60' : '18'} />
  </svg>
)

export const PinMarker = ({ onDragEnd, site, ...svgProps }: PinMarkerProps): JSX.Element => {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [point, setPoint, updatePoint] = useUpdatableState<Point>(site, [site])

  const handleDrag = (event: { lngLat: [number, number] }) => {
    updatePoint((draft) => {
      draft.longitude = event.lngLat[0]
      draft.latitude = event.lngLat[1]
    })
  }

  const handleDragEnd = (event: { lngLat: [number, number] }) => {
    if (!onDragEnd) return
    onDragEnd({ longitude: event.lngLat[0], latitude: event.lngLat[1] })
  }

  return (
    <Marker
      latitude={point.latitude}
      longitude={point.longitude}
      draggable={!!onDragEnd}
      css={onDragEnd ? undefined : { pointerEvents: 'none' }}
      onDrag={handleDrag}
      onDragEnd={handleDragEnd}
    >
      <div
        css={{
          height: 40,
          width: 40 / (216 / 145),
          transform: `translate(-50%,-100%)`,
        }}
      >
        <PinSvg {...svgProps} />
      </div>
    </Marker>
  )
}
