/** @jsxImportSource @emotion/react */
import React, { useState, useRef, useEffect } from 'react'
import Button from '@mui/material/Button'
import Dialog from '@mui/material/Dialog'
import DialogActions from '@mui/material/DialogActions'
import DialogContent from '@mui/material/DialogContent'
import DialogTitle from '@mui/material/DialogTitle'

import { NumberField } from '../NumberField'

interface TimeEntryDialogProps {
  isOpen: boolean
  onConfirm: (metrics: { time: number }) => void
}

export const TimeEntryDialog = (props: TimeEntryDialogProps): JSX.Element => {
  const { isOpen } = props
  return (
    <Dialog open={isOpen} disableEscapeKeyDown>
      <DialogBody {...props} />
    </Dialog>
  )
}

const DialogBody = ({ onConfirm }: TimeEntryDialogProps): JSX.Element => {
  const [time, setTime] = useState<number | null>(null)
  const inputRef = useRef<HTMLInputElement | null>(null)

  const hasErrors = time === null

  const handleConfirm = () => {
    if (time !== null) onConfirm({ time })
  }

  useEffect(() => {
    inputRef.current?.focus()
  }, [inputRef.current])

  return (
    <>
      <DialogTitle>Metrics</DialogTitle>
      <DialogContent>
        <div css={{ padding: '0.5em' }}>
          <NumberField
            inputRef={inputRef}
            label="Hours"
            value={time}
            onChange={setTime}
            error={hasErrors}
            min={0}
            onKeyDown={(e) => {
              if (e.key !== 'Enter') return
              e.preventDefault()
              handleConfirm()
            }}
          />
        </div>
      </DialogContent>
      <DialogActions>
        <Button disabled={time === null} onClick={handleConfirm}>
          OK
        </Button>
      </DialogActions>
    </>
  )
}
