/** @jsxImportSource @emotion/react */
import React, { Suspense } from 'react'
import MuiTextField from '@mui/material/TextField'
import MuiAutocomplete from '@mui/material/Autocomplete'
import { RecoilValue, useRecoilValue } from 'recoil'
import Skeleton from '@mui/material/Skeleton'

import { ReadOnlyField } from '../ReadOnlyField'
import { CommonFieldProps } from '../../types'

export interface Option<T> {
  value: T
  label: string
  disabled?: boolean
}

interface EditableAutocompleteFieldProps<T> extends CommonFieldProps<T | null> {
  isClearable?: boolean
  options: Option<T>[]
  optionsSelector?: never
  valueLabel?: never
}

interface SuspensedEditableAutocompleteFieldProps<T> extends CommonFieldProps<T | null> {
  options?: never
  optionsSelector: RecoilValue<Option<T>[]>
  valueLabel: string | null
}

export type AutocompleteFieldProps<T> = CommonFieldProps<T | null> & {
  isClearable?: boolean
  label?: string
} & (EditableAutocompleteFieldProps<T> | SuspensedEditableAutocompleteFieldProps<T>)

const EditableAutocompleteField = <T,>({
  // CommonFieldProps
  className,
  disabled,
  error,
  gridArea,
  label,
  onChange,
  value,

  // EditableAutocompleteFieldProps
  isClearable,
  options,
}: EditableAutocompleteFieldProps<T>): JSX.Element => {
  const foundValue = options.find((option) => option.value === value) || null

  return (
    <MuiAutocomplete
      className={className}
      // disablePortal
      options={options}
      disabled={disabled}
      getOptionLabel={(option) => option.label}
      getOptionDisabled={(option) => !!option.disabled}
      renderInput={(params) => (
        <MuiTextField
          {...params}
          label={label}
          error={error}
          // InputLabelProps={{ shrink: !!foundValue }} // TODO: figure out why I needed this in the first place; is it due to valueLabel somehow?
        />
      )}
      onChange={(event, selected) => {
        onChange(selected && selected.value)
      }}
      size="small"
      value={foundValue}
      css={{ gridArea }}
      disableClearable={!isClearable}
    />
  )
}

const SuspensedEditableAutocompleteField = <T,>({
  optionsSelector,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  valueLabel, // deliberately discarded
  ...rest
}: SuspensedEditableAutocompleteFieldProps<T>) => {
  const options = useRecoilValue(optionsSelector)
  return <EditableAutocompleteField options={options} {...rest} />
}

const mismatchedPropsErrorMessage =
  'AutocompleteField requires either `options`, or `optionsSelector` with `valueLabel`.'

export const AutocompleteField = <T,>({
  options,
  optionsSelector,
  readOnly,
  valueLabel,
  ...restProps
}: AutocompleteFieldProps<T>): JSX.Element => {
  const isFixedType = !!options
  const isSelectorType = optionsSelector && typeof valueLabel !== 'undefined'
  if (isFixedType && isSelectorType) throw new Error(mismatchedPropsErrorMessage) // should never be reached

  if (readOnly) {
    if (isFixedType) {
      const foundValueLabel = options.find((option) => option.value === restProps.value)?.label ?? ''
      return <ReadOnlyField {...restProps} value={foundValueLabel} />
    } else if (isSelectorType) {
      return <ReadOnlyField {...restProps} value={valueLabel} />
    }
  } else {
    if (isFixedType) {
      return <EditableAutocompleteField options={options} {...restProps} />
    } else if (isSelectorType) {
      return (
        <Suspense fallback={<ReadOnlyField {...restProps} value={<Skeleton />} />}>
          <SuspensedEditableAutocompleteField
            optionsSelector={optionsSelector}
            valueLabel={valueLabel}
            {...restProps}
          />
        </Suspense>
      )
    }
  }

  throw new Error(mismatchedPropsErrorMessage) // should never be reached
}
