/** @jsxImportSource @emotion/react */
import React from 'react'

import { ReadOnlyField } from '../ReadOnlyField'

export interface ProgressFieldProps {
  // CommonFieldProps
  gridArea?: string
  label: string
  value: number

  // ProgressField
  doneCount: number
  totalCount: number
}

export const ProgressField = ({
  doneCount,
  gridArea, //
  label,
  totalCount,
  value,
}: ProgressFieldProps): JSX.Element => {
  const percent = `${Math.floor(value * 100)}%`

  const roValue = (
    <div
      css={{
        height: '1em',
        width: '100%',
        position: 'relative',
      }}
    >
      <div css={{ position: 'absolute', background: 'lightgrey', width: '100%', height: '110%', top: '16%' }} />
      <div css={{ position: 'absolute', background: '#42d64c', width: percent, height: '110%', top: '16%' }} />
      <div css={{ position: 'absolute', width: '100%', textAlign: 'center' }}>
        {doneCount} of {totalCount} ({percent})
      </div>
    </div>
  )

  return <ReadOnlyField label={label} value={roValue} gridArea={gridArea} minWidth={120} />
}
