import Excel from 'exceljs'
import saveAs from 'file-saver'
import { get } from 'lodash'

export const downloadWorkbook = async (workbook: Excel.Workbook, filename: string): Promise<void> => {
  const buffer = await workbook.xlsx.writeBuffer()
  const blob = new Blob([buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' })
  saveAs(blob, filename)
}

export const downloadWorkbookAsCsv = async (workbook: Excel.Workbook, filename: string): Promise<void> => {
  const buffer = await workbook.csv.writeBuffer()
  const blob = new Blob([buffer], { type: 'text/csv' })
  saveAs(blob, filename)
}

export const downloadDataAsCsv = async <T>(
  { rows, columns }: { columns: Partial<Excel.Column>[]; rows: T[] },
  filename: string
): Promise<void> => {
  const wb = new Excel.Workbook()
  const ws = wb.addWorksheet('data')

  const processedRows = rows.map((row) =>
    columns.reduce<Record<string, string | number | boolean | null | undefined>>((acc, col) => {
      const colKey = col.key
      if (!colKey) throw new Error(`A column key is missing.`)
      return { ...acc, [colKey]: get(row, colKey) }
    }, {})
  )

  ws.columns = columns
  ws.addRows(processedRows)

  downloadWorkbookAsCsv(wb, filename)
}
