import React from 'react'
import Skeleton from '@mui/material/Skeleton'
import { useRecoilValue } from 'recoil'

import { ReadOnlyField } from '../ReadOnlyField'
import { ProgressField } from '../ProgressField'
import { withSuspense } from '../withSuspense'
import { selectBoxSamplesByBoxId } from '../../data/selectors'

export const IdentificationProgressField = withSuspense<{
  boxId: number
  gridArea: string
  value: number
}>(
  ({ boxId, value, ...restProps }) => {
    const boxSamples = useRecoilValue(selectBoxSamplesByBoxId(boxId))
    const doneCount = boxSamples.filter((boxSample) => boxSample.identificationEndDate).length
    const totalCount = boxSamples.length
    return (
      <ProgressField
        {...restProps}
        label="Identification"
        value={value}
        doneCount={doneCount}
        totalCount={totalCount}
      />
    )
  },
  (props): JSX.Element => <ReadOnlyField {...props} label="Identification" value={<Skeleton />} minWidth={120} />
)
