/** @jsxImportSource @emotion/react */
import React from 'react'
import MuiSkeleton from '@mui/material/Skeleton'

import { withSuspense } from '../withSuspense'
import { Point } from '../../types'
import { ReadOnlyField } from '../ReadOnlyField'
import { selectUsStateAtCoordinates } from '../../data/selectors'
import { useRecoilValue } from 'recoil'
import { ReadOnlyFieldProps } from '../ReadOnlyField'

export interface ReadOnlyUsStateFieldProps extends Omit<ReadOnlyFieldProps, 'value'> {
  point: Point
}

export const UsStateAtPointField = withSuspense(
  ({
    point: { latitude, longitude }, // ensure no addition properties are passed to Recoil selector serializer
    ...restProps
  }: ReadOnlyUsStateFieldProps): JSX.Element => {
    const usState = useRecoilValue(selectUsStateAtCoordinates({ latitude, longitude }))
    return <ReadOnlyField {...restProps} value={usState && usState.abbreviation} />
  },
  (props) => <ReadOnlyField {...props} value={<MuiSkeleton sx={{ width: '3em' }} />} />
)
