import React from 'react'
import { useRecoilValue } from 'recoil'

import { selectPredictorById, selectNull } from '../../data/selectors'
import { withSuspense } from '../withSuspense'
import { PredictorDetail as Component } from './PredictorDetail'
import { Fallback } from './Fallback'

export type PredictorDetailProps =
  | {
      predictorId: string | number
      isNew?: never
    }
  | {
      predictorId?: never
      isNew: true // unconsumed; used to positively differentiate modes
    }

export const PredictorDetail = withSuspense(({ predictorId }: PredictorDetailProps): JSX.Element => {
  const predictor = useRecoilValue(predictorId ? selectPredictorById(Number(predictorId)) : selectNull)

  if (predictorId && !predictor) return <h2>Predictor {predictorId} not found.</h2>

  return <Component predictor={predictor} />
}, Fallback)
